<template>
    <div class="grid">
      <HeaderNav  v-bind:title="'Send ' + (networkName || 'BSV')"/>

      <div class="s_results form">
              <form class="ui form" @submit.prevent="sendTx">

                <div class="field-block">
                  <h4>Address</h4>
                  <input type="text" name="receiverAddress" v-model="receiverAddress" placeholder="Address" required autocomplete="off">
                </div>

                <div class="field-block">
                  <h4>Amount ({{ networkName || 'BSV' }})</h4>
                  <input type="number" step="0.00000001" name="amount" v-model="amount" :placeholder="'Amount (' + (networkName || 'BSV') + ')'" required autocomplete="off">
                  <p style="color:#666;font-size:small" v-if="amount">({{ amountSat }} satoshis)</p>
                </div>

                <div class="field-block">
                  <h4>Message (optional)</h4>
                  <input type="text" name="opReturnItem" v-model="opReturnItem" placeholder="Message" autocomplete="off"> <a href="javascript:void(0);" @click="addToOpReturn()">[Add]</a>
                  <p style="color:#666;font-size:small" v-if="opReturn.length > 0">{{ opReturn }} <a href="javascript:void(0);" @click="deleteOpReturn()">[Remove]</a></p>
                </div>

                <div class="form-buttons">
                  <input value="Send" type="submit" :disabled="submitted">
                </div>
              </form>
      </div>
      <Footer/>
    </div>

</template>

<script>
import HeaderNav from '@/components/Header/Nav'
import Footer from '@/components/Footer'

export default {
  name: 'WalletSend',
  mixins: [],
  components: {
    HeaderNav,
    Footer
  },
  data () {
    return {
      networkName: window.networkName,
      receiverAddress: null,
      amount: null,
      submitted: false,
      opReturnItem: null,
      opReturn: []
    }
  },
  head: function () {
    return {
      title: {
        inner: 'Send ' + (this.networkName || 'BSV')
      }
    }
  },
  mounted () {
    this.loginRequired()
  },
  computed: {
    amountSat: function () {
      if (this.amount) {
        return satoshiBitcoin.toSatoshi(this.amount)
      }
      return
    }
  },
  watch: {
    txBroadcasting: function (val) {
      if (val === false) {
        this.submitted = false
      }
    },
    txSent: function (val) {
      if (val === true) {
        this.receiverAddress = ''
        this.amount = ''
        this.opReturn = []
      }
    }
  },
  methods: {
    addToOpReturn () {
      if (this.opReturnItem !== null) {
        this.opReturn.push(this.opReturnItem)
        this.opReturnItem = null
      }
    },
    deleteOpReturn () {
      this.opReturn = []
    },
    sendTx () {
      if (this.loggedSpendMode()) {
        // TODO: add address validator
        let convertedAddress
        try {
          convertedAddress = window.convertAddress(this.receiverAddress, 'cashaddr')
        }
        catch(err) {
          this.$notify({
            group: 'wallet',
            title: 'Wallet',
            text: 'Invalid receiver address',
            type: 'error'
          })
          return
        }
        this.submitted=true
        let toArray = []
        let toElement = {}
        toElement['address'] = convertedAddress
        toElement['value'] = parseInt(this.amountSat)
        toArray.push(toElement)

        this.sendTransaction([], toArray)
      } else {
        this.loginRequired()
      }
    }
  }
}
</script>



