<template>
  <span>
    <div class="topic-title">
      <span v-if="usernames[requestAddress]">{{ usernames[requestAddress] }}</span>
      <span v-else>{{ address.substr(0, 12) }}...</span>
    </div>
    <div class="topic-name" v-if="userLegacyAddress && requestLegacyAddress != userLegacyAddress">
      <button class="button-active" v-if="followedUsers.includes(requestAddress)" @click="unfollowAddress(requestAddress)">Following</button>
      <button class="button" v-else @click="followAddress(requestAddress)">Follow</button>
      <button class="button-active" v-if="mutedUsers.includes(requestLegacyAddress)" @click="removeMutedUser(requestAddress)">Muted</button>
      <button class="button" v-else @click="addMutedUser(requestAddress)">Mute</button>
    </div>
    <div class="topics">
<!--
      <div class="topic-name"><router-link :to="'/user/' + requestLegacyAddress">PosterCoins</router-link>: {{ actionsBalances[requestAddress] }} P</div>
// -->
      <div class="topic-name"><router-link :to="'/user/' + requestLegacyAddress">Posts</router-link></div>
      <div class="topic-name"><router-link :to="'/user/likes/' + requestLegacyAddress">Likes</router-link></div>
      <div class="topic-name"><router-link :to="'/user/following/' + requestLegacyAddress">Following</router-link></div>
      <div class="topic-name"><router-link :to="'/user/followers/' + requestLegacyAddress">Followers</router-link></div>
      <div class="topic-name"><router-link :to="'/my/muted'" v-if="userLegacyAddress && requestLegacyAddress == userLegacyAddress">Muted</router-link></div>
    </div>
  </span>
</template>

<script>
import { mapState, mapActions } from 'vuex'

  export default {
      name: 'UserNav',
      props: {
        address: String
      },
      data () {
        return {
          userLegacyAddress: window.convertAddress(this.getAddress(), 'legacy'),
          requestLegacyAddress: window.convertAddress(this.address, 'legacy'),
          requestAddress: window.convertAddress(this.address)
        }
      },
      components: {
      },
      mounted () {
      },
      computed: {
        ...mapState([
          'actionsBalances',
          'followedUsers',
          'mutedUsers',
          'usernames'
        ])
      },
      watch: {
      },
      methods: {
        ...mapActions([
          'addMutedUser',
          'removeMutedUser',
          'addToFollowedUsers',
          'removeFromFollowedUsers'
        ]),
        followAddress (address) {
          if (this.loginRequired()) {
            return
          }
          this.follow(address)
          this.addToFollowedUsers(address)
        },
        unfollowAddress (address) {
          if (this.loginRequired()) {
            return
          }
          this.unfollow(address)
          this.removeFromFollowedUsers(address)
        }
      }
  };
</script>

<style>
</style>



