<template>
  <div class="stream-item" v-bind:class="{ 'stream-item-single': (isLarge || !showActions) }" v-if="(item.prefix === '6d02' && item.content1) || item.content2">


    <div class="stream-item-text-container" v-if="!isLarge">
      <p class="post-context" v-if="item.prefix === '6d03' && parentPosters[item.parentTxId]">Reply to <router-link class="post-context-link" :to="'/user/' + parentPosters[item.parentTxId]">{{ usernames[parentPosters[item.parentTxId]] ? usernames[parentPosters[item.parentTxId]] : parentPosters[item.parentTxId].substr(0, 12) + '...' }}</router-link></p>
      <p class="post-context" v-if="item.prefix === '6d03' && !parentPosters[item.parentTxId]">Reply to unidentified post</p>
      <p class="post-context" v-if="item.prefix === '6d0c'">In topic '{{ item.content1 }}'</p>
    </div>

    <div class="stream-item-header">
      <router-link :to="'/user/' + getLegacyAddress(item.sender)" class="account-group">
        <img class="avatar" v-if="profilePictures[item.sender]" :src="profilePictures[item.sender]" alt="">
        <img class="avatar" v-else src="../../assets/images/profile-picture-normal.png" alt="">
          <span class="full-name-group">
            <strong class="full-name text-truncate" v-bind:class="{ 'full-name-large': isLarge }" v-if="usernames[item.sender]">{{ usernames[item.sender] }}</strong>
            <strong class="full-name text-truncate" v-bind:class="{ 'full-name-large': isLarge }" v-else>{{ item.sender.substr(0, 12) }}...</strong>
            <span class="user-badges">
                <strong class="full-name text-truncate" v-if="followedUsers.includes(item.sender) && !isLarge"><i class="fas fa-star following"></i></strong>
            </span>
          </span>
      </router-link>
      <span v-if="!isLarge">
        <span class="date" v-if="item.timestamp">&nbsp;· {{ timeSince(item.timestamp) }}</span>
        <span class="date" v-else>&nbsp;· now</span>
      </span>
      <span class="item-dropdown" v-if="!isLarge">
        <a href="javascript:void(0)" class="item-dropdown-opener" @click="showItemDropdown = !showItemDropdown"><i class="fas fa-chevron-down"></i></a>
          <ul v-bind:class="{ 'item-dropdown-menu': !showItemDropdown, 'item-dropdown-menu-active': showItemDropdown }" id="itemDropdown">
            <li><a :href="blockExplorerRpc + '/tx/' + item.txId">View in block explorer</a></li>
            <li><a href="javascript:void(0)" @click="addMutedUser(item.sender);showItemDropdown = !showItemDropdown">Mute user</a></li>
          </ul>
        </span>
      <span class="large-following" v-if="isLarge">
        <button class="button-active" v-if="followedUsers.includes(item.sender)" @click="unfollowAddress(item.sender)">Following</button>
        <button class="button" v-else @click="followAddress(item.sender)">Follow</button>
      </span>
    </div>

    <div class="stream-item-text-container" @click="displayModal(item, 'displayModal')">
      <p class="item-text" v-bind:class="{ 'item-text-large': isLarge }" v-if="item.prefix === '6d02'"><auto-link :content="item.content1" :image="postShowImages" :embed="postShowVideosEmbed" :image-attr="{'class': 'responsive-image'}" safe removeHTTP></auto-link></p>
      <p class="item-text" v-bind:class="{ 'item-text-large': isLarge }" v-else><auto-link :content="item.content2" :image="postShowImages" :embed="postShowVideosEmbed" :image-attr="{'class': 'responsive-image'}" safe removeHTTP></auto-link></p>
    </div>

    <div class="stream-item-text-container" v-if="isLarge">
      <p class="date-large" v-if="item.timestamp">{{ largeDatetime(item.timestamp) }}</p>
      <p class="date-large" v-else>{{ largeDatetime(Date.now()/1000) }}</p>
      <p>
        <span v-for="(likeAddress, likeIndex) in likedBy">
          <router-link :to="'/user/' + getLegacyAddress(likeAddress)">
            <img class="avatar-small" v-if="profilePictures[likeAddress]" :src="profilePictures[likeAddress]" :alt="usernames[likeAddress]">
            <img class="avatar-small" v-else src="../../assets/images/profile-picture-normal.png" alt="">
          </router-link>
        </span>
      </p>
    </div>

    <div class="stream-item-footer" v-if="showActions">
      <span>
        <i class="fas fa-heart icon" v-if="myPostLikes[item.txId]"></i><i class="far fa-heart icon" v-else @click="likeItem(item.txId, item.sender, 0)"></i><span class="post-action-count"><span v-if="likesCount[item.txId]">{{ likesCount[item.txId] }}</span><span v-else>&nbsp;</span></span>
        <i class="fas fa-comment icon" v-if="myPostReplies[item.txId]" @click="replyModal(item, 'replyModal')"></i><i class="far fa-comment icon" v-else @click="replyModal(item, 'replyModal')"></i><span class="post-action-count"><span v-if="repliesCount[item.txId]">{{ repliesCount[item.txId] }}</span><span v-else>&nbsp;</span></span>
          <form class="tip-form" @submit.prevent="likeItem(item.txId, item.sender, tipAmount)">
            <input type="number" min="546" required autocomplete="off" class="tip-input" v-model="tipAmount" placeholder="Tip">
            <button class="fas fa-hand-holding-usd icon"></button>
          </form>
      </span>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState, mapActions } from 'vuex'

export default {
  name: 'PostListItem',
  mixins: [],
  props: {
    showActions: {
      type: Number,
      default: 1
    },
    isLarge: {
      type: Number,
      default: 0
    },
    item: Object,
    topics: Array
  },
  components: {
  },
  data () {
    return {
      postShowImages: window.postShowImages,
      postShowVideosEmbed: window.postShowVideosEmbed,
      blockExplorerRpc: window.blockExplorerRpc,
      likedBy: [],
      tipAmount: '',
      showItemDropdown: false
    }
  },
  mounted () {
    if (this.isLarge) {
      this.getAddressLikesPerPost(this.item.txId)
    }
  },
  computed: {
    ...mapState([
      'followedUsers',
      'myPostLikes',
      'myPostReplies',
      'likesCount',
      'repliesCount',
      'usernames',
      'profilePictures',
      'parentPosters'
    ])
  },
  watch: {
    item: function (val) {
      if (this.isLarge) {
        this.likedBy = []
        this.getAddressLikesPerPost(val.txId)
      }
    }
  },
  methods: {
    ...mapActions([
      'addNewUsersData',
      'addMutedUser',
      'changeCurrentPost',
      'addToFollowedUsers',
      'removeFromFollowedUsers',
      'addToMyLikes'
    ]),
    getLegacyAddress (address) {
      return window.convertAddress(address, 'legacy')
    },
    likeItem (txId, address, amount) {
      if (this.loginRequired()) {
        return
      }
      this.likePost(txId, address, amount)
      this.tipAmount = ''
      this.addToMyLikes(txId)
    },
    followAddress (address) {
      if (this.loginRequired()) {
        return
      }
      this.follow(address)
      this.addToFollowedUsers(address)
    },
    unfollowAddress (address) {
      if (this.loginRequired()) {
        return
      }
      this.unfollow(address)
      this.removeFromFollowedUsers(address)
    },
    displayModal (item, modal) {
      this.changeCurrentPost(item)
      this.openModal(modal)
    },
    replyModal (item, modal) {
      if (this.loginRequired()) {
        return
      }
      if (this.profilePictures[item.sender] !== undefined) {
        item['picture'] = this.profilePictures[item.sender]
      }
      if (this.usernames[item.sender] !== undefined) {
        item['username'] = this.usernames[item.sender]
      } else {
        item['username'] = item.sender
      }
      if (document.getElementById('displayModal').style.display === 'block') {
        this.closeModal('displayModal')
      }
      this.changeCurrentPost(item)
      this.openModal(modal)
    },

    getAddressLikesPerPost (txId) {
      let findQuery = {}
      findQuery['$and'] = []
      findQuery['$and'].push({'out.b0':{ 'op': 106 }})
      findQuery['$and'].push({'out.h1':'6d04'})

      let txIdsSearchArray = []
      txIdsSearchArray.push(reverseHex(txId))
      findQuery['$and'].push({'out.h2': {'$in': txIdsSearchArray}})

      let query = {
        'v': 3,
        'q': {
          'find': findQuery,
          'project': { 'in': 1, 'out.$': 1, 'blk': 1, 'tx': 1 },
          'skip': 0,
          'limit': 10
        },
        'r': {
          'f': '[ .[] | { sender: .in[0].e.a} ]'
        }
      }

      let b64 = Base64.encode(JSON.stringify(query))
      let url = window.bitdbNode + b64

      let header
      if (window.bitdbApiKey) {
        header = {
          headers: { key: window.bitdbApiKey }
        }
      }

      axios
        .get(url, header)
        .then(response => {
          if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
            let result = []
            if (response.data.u !== undefined) {
              result = result.concat(response.data.u.reverse())
            }
            if (response.data.c !== undefined) {
              result = result.concat(response.data.c)
            }

            for (let i = 0; i < result.length; ++i) {
              if (!(this.likedBy.includes(result[i].sender))) {
                this.likedBy.push(result[i].sender)
                console.log(result[i].sender)
              }
            }
            this.addNewUsersData({ 'address': this.getAddress(), 'newTxIds': [], 'newAddresses': [...this.likedBy] })
          }
        })
        .catch(error => {
          console.log(JSON.stringify(error))
        })
    }
  }
}
</script>
<style>
.post-context{
  color: #657786;
  font-size: 12px;
  margin-top: -3px;
  margin-bottom: 3px;
}
a.post-context-link{
  color: #657786;
}
a.post-context-link:hover{
  color: #D02B55;
}
img.avatar-small{
  width: 24px;
  height: 24px;
  border-radius: 50%;
}
</style>



