export default {
  data: function () {
    return {
      actionIcons: {},
      actionsWalletError: null
    }
  },
  mounted () {
    this.actionIcons['iconType1.000001'] = 'fas fa-bicycle'
    this.actionIcons['iconType1.000002'] = 'fas fa-cat'
    this.actionIcons['iconType1.000003'] = 'fas fa-dog'
    this.actionIcons['iconType1.000004'] = 'fas fa-cross'
    this.actionIcons['iconType1.000005'] = 'fas fa-moon'
    this.actionIcons['iconType1.000006'] = 'fas fa-pastafarianism'
    
    this.actionIcons['iconType2.000001'] = 'fas fa-chess-king'
    this.actionIcons['iconType2.000002'] = 'fas fa-chess-queen'
    this.actionIcons['iconType2.000003'] = 'fas fa-bolt'
    this.actionIcons['iconType2.000004'] = 'fas fa-dragon'
  },
  created () {
  },
  methods: {
    sendActionCoins (address, amount, itemType = null, itemNumber = null) {
      console.log('sendActionCoins: ' + address)
      if (address === undefined) {
        this.actionsWalletError = 'Address required'
      }
      amount = parseInt(amount)
      if (amount <= 0) {
        this.actionsWalletError = 'Invalid amount'
      }
      let toArray = []
      let toElement = {}
      toElement['address'] = address
      toElement['value'] = parseInt(amount)
      toArray.push(toElement)

      let data = []
      data.push('0x505354FF')  // token id
      data.push('0x01')  // action id
      if (/^[0-9A-F]{4}$/i.test(itemType) && /^[0-9A-F]{6}$/i.test(itemNumber)) {
        data.push('0x' + itemType)  // item type (if any)
        data.push('0x' + itemNumber)  // item number
      }
      this.sendTransaction(data, toArray)
    }
  }
}



