import Vue from 'vue'
import Vuex from 'vuex'
import { mutations } from './mutations'
import actions from './actions'
import getters from './getters'
//import plugins from './plugins'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    unconfirmedNotificationsChecked: parseInt(localStorage.getItem('unconfirmedNotificationsChecked')) || 0,
    notificationChecked: parseInt(localStorage.getItem('notificationChecked')) || parseInt((new Date().getTime())/1000),
    unconfirmedMessagesChecked: parseInt(localStorage.getItem('unconfirmedMessagesChecked')) || 0,
    messageChecked: parseInt(localStorage.getItem('messageChecked')) || parseInt((new Date().getTime())/1000),
    localPost: null,
    currentPost: null,
    loadingFollowers: true,
    followedUsers: [],
    followedUsersLocal: [],
    followedUsersOnchain: [],
    mutedUsers: JSON.parse(localStorage.getItem('mutedUsers')) || [],
    textFilters: JSON.parse(localStorage.getItem('textFilters')) || [],
    myBalance: null,
    myPostLikes: {},
    myPostReplies: {},
    myTxIds: [],
    crawledAddresses: [],
    crawledTxIds: [],
    crawledParentTxIds: [],
    profilePictures: {},
    usernames: {},
    parentPosters: {},
    actionsBalances: {},
    actionsCreated: {},
    actionsReceived: {},
    actionsSent: {},
    likesCount: {},
    repliesCount: {},
  },
  getters,
  actions,
  mutations
//  plugins
})



