<template>
  <div>
    <p>Poster is social network client using the Memo protocol. It's powered by Bitdb and only communicates with a Bitdb node.</p>
  	<p>User actions (post, reply, like, ...) are stored in the Bitcoin (SV) blockchain and cannot be altered or deleted. User settings are stored in the user's browser or in the Bitcoin (SV) blockchain.</p>
    <p>Poster does not store any user data.</p>
    <p>Original memo protocol: <a href="https://memo.cash/protocol" target="_blank">https://memo.cash/protocol</a></p>
    <p><b>Implemented protocol:</b></p>
      <table class="protocol">
        <tr>
          <th>Action</th>
          <th>Prefix</th>
          <th>Values</th>
          <th>Status</th>
        </tr>
        <tr>
          <td>Set name</td>
          <td>0x6d01</td>
          <td>name(217)</td>
          <td>Implemented</td>
        </tr>
        <tr>
          <td>Post memo</td>
          <td>0x6d02</td>
          <td>message(217)</td>
          <td>Implemented</td>
        </tr>
        <tr>
          <td>Reply to memo</td>
          <td>0x6d03</td>
          <td>txhash(32), message(184)</td>
          <td>Implemented</td>
        </tr>
        <tr>
          <td>Like / tip memo</td>
          <td>0x6d04</td>
          <td>txhash(32)</td>
          <td>Implemented</td>
        </tr>
        <tr>
          <td>Set profile text</td>
          <td>0x6d05</td>
          <td>message(217)</td>
          <td>Implemented</td>
        </tr>
        <tr>
          <td>Follow user</td>
          <td>0x6d06</td>
          <td>address(35)</td>
          <td>Implemented</td>
        </tr>
        <tr>
          <td>Unfollow user</td>
          <td>0x6d07</td>
          <td>address(35)</td>
          <td>Implemented</td>
        </tr>
        <tr>
          <td>Set profile picture</td>
          <td>0x6d0a</td>
          <td>url(217)</td>
          <td>Implemented</td>
        </tr>
        <tr>
          <td>Post topic message</td>
          <td>0x6d0c</td>
          <td>topic_name(variable), message(214 - topic length)</td>
          <td>Implemented</td>
        </tr>
      </table>

      <p><b>Poster protocol:</b></p>
        <table class="protocol">
          <tr>
            <th>Action</th>
            <th>Domain prefix</th>
            <th>Action prefix</th>
            <th>Values</th>
            <th>Status</th>
          </tr>
          <tr>
            <td>Invite</td>
            <td>16Uc2K99dS3VZ9v7mbZSFUh8gJ2432GKiC</td>
            <td>0x01</td>
            <td>address(35)</td>
            <td>Planned</td>
          </tr>
          <tr>
            <td>Set min notificaton amount (reply)</td>
            <td>16Uc2K99dS3VZ9v7mbZSFUh8gJ2432GKiC</td>
            <td>0x20</td>
            <td>amount(4)</td>
            <td>Implemented</td>
          </tr>
          <tr>
            <td>Set min notificaton amount (message)</td>
            <td>16Uc2K99dS3VZ9v7mbZSFUh8gJ2432GKiC</td>
            <td>0x21</td>
            <td>amount(4)</td>
            <td>Implemented</td>
          </tr>
          <tr>
            <td>Send direct message</td>
            <td>1FjFiAJdhZ74NXsh9J472f7qkYjpiW9to9</td>
            <td>0x01</td>
            <td>address(35)</td>
            <td>message(609)</td>
            <td>Planned</td>
          </tr>
        </table>

  </div>
</template>

<script>
  export default {
    name: 'About',
    components: {
    }
  };
</script>

<style>
.protocol th, .protocol td {
  text-align: left;
  padding-left: 15px;
}
</style>



