<template>
  <div class="grid grey">
    <HeaderNav/>

    <div class="s_left">
      <UserNav :address="userAddress" />
    </div>

    <div  class="s_results" v-if="mutedAddress.length > 0">
      <p class="section-title">
        My muted users
      </p>
      <div class="stream-item" v-for="(item, index) in mutedAddress">
          <div class="stream-item-header">
            <router-link :to="'/user/' + mutedUsers[index]" class="account-group">
              <img class="avatar" v-if="profilePictures[item]" :src="profilePictures[item]" alt="">
              <img class="avatar" v-else src="../../assets/images/profile-picture-normal.png" alt="">
                <span class="full-name-group">
                  <strong class="full-name text-truncate" v-if="usernames[item]">{{ usernames[item] }}</strong>
                  <strong class="full-name text-truncate" v-else>{{ item.substr(0, 12) }}...</strong>
                  <span class="user-badges">
                      <strong class="full-name text-truncate" v-if="followedUsers.includes(item)"><i class="fas fa-star following"></i></strong>
                  </span>
                </span>
            </router-link>
            <a href="javascript:void(0);" @click="unmuteUser(item, index)">[x]</a>
          </div>
      </div>
    </div>
    <div class="s_results" v-else>
      <div class="n_results">No muted users</div>
    </div>

    <div class="s_right topics">
    </div>

    <Footer/>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import HeaderNav from '@/components/Header/Nav'
import Footer from '@/components/Footer'
import UserNav from '@/components/User/Nav'

export default {
  name: 'UserMuted',
  mixins: [],
  props: {
  },
  data () {
    return {
      userAddress: window.convertAddress(this.getAddress()),
      mutedAddress: []
    }
  },
  head: function () {
    return {
      title: {
        inner: 'My muted users'
      },
      // Meta tags
      meta: [
        { name: 'application-name', content: 'Poster.cash' },
        { name: 'twitter:title', content: 'My muted users' },
        { itemprop: 'name', content: 'My muted users' },
        { property: 'og:title', content: 'My muted users' },
      ]
    }
  },
  components: {
    HeaderNav,
    Footer,
    UserNav
  },
  mounted () {
    this.changeMutedAddresses()
  },
  created() {
  },
  computed: {
    ...mapState([
      'followedUsers',
      'mutedUsers',
      'profilePictures',
      'usernames'
    ])
  },
  watch: {
  },
  methods: {
    ...mapActions([
      'addNewUsersData',
      'removeMutedUser'
    ]),
    unmuteUser (address, index) {
      this.removeMutedUser(address)
      this.mutedAddress.splice(index, 1)
    },
    changeMutedAddresses() {
      this.mutedAddress = []
      for (let i = 0; i < this.mutedUsers.length; ++i) {
        this.mutedAddress.push(window.convertAddress(this.mutedUsers[i]))
      }

      this.addNewUsersData({ 'address': null, 'newTxIds': [], 'newAddresses': [...this.mutedAddress] })
    }
  }
}
</script>

<style>
</style>



