import { mapState, mapActions } from 'vuex'

export default {
  mixins: [],
  data: function () {
    return {
      posterReplyNotificationAmount: null,
      posterReplyNotificationAmountLoaded: false,
      posterMessageNotificationAmount: null,
      posterMessageNotificationAmountLoaded: false
    }
  },
  mounted () {
  },
  watch: {
  },
  computed: {
  },
  methods: {
    setReplyNotificationAmount (amount) {
      if (isNaN(amount)) {
        this.$notify({
          group: 'poster',
          title: 'Poster',
          text: 'Tip amount is not a number',
          type: 'error'
        })
        return
      }
      let hexAmount = parseInt(amount).toString(16)
      if (hexAmount.length % 2) {
        hexAmount = '0' + hexAmount
      }
      const data = ['16Uc2K99dS3VZ9v7mbZSFUh8gJ2432GKiC', '0x20', '0x' + hexAmount]
      let toArray = []
      this.sendTransaction(data, [])
    },
    getReplyNotificationAmount (address) {
      console.log('getReplyNotificationAmount')
      this.posterReplyNotificationAmountLoaded = false
      let findQuery = {}
      findQuery['$and'] = []
      findQuery['$and'].push({'out.b0':{ 'op': 106 }})
      findQuery['$and'].push({'out.s1':'16Uc2K99dS3VZ9v7mbZSFUh8gJ2432GKiC'})
      findQuery['$and'].push({'out.h2':'20'})
      findQuery['$and'].push({'in.e.a': ''  + address + ''})

      let query = {
        'v': 3,
        'q': {
          'find': findQuery,
          'project': { 'out.$': 1 },
          'limit': 1
        },
        'r': {
          'f': '[ .[] | { amount: .out[0].h3 } ]'
        }
      }

      let b64 = Base64.encode(JSON.stringify(query))
      let url = window.bitdbNode + b64

      let header
      if (window.bitdbApiKey) {
        header = {
          headers: { key: window.bitdbApiKey }
        }
      }

      axios
        .get(url, header)
        .then(response => {
          if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
            if (response.data.u !== undefined && response.data.u.length > 0) {
              this.posterReplyNotificationAmount = parseInt(response.data.u[0].amount, 16)
            }
            if ((response.data.u === undefined || response.data.u.length === 0) && response.data.c !== undefined && response.data.c.length > 0) {
              this.posterReplyNotificationAmount = parseInt(response.data.c[0].amount, 16)
            }
            this.posterReplyNotificationAmountLoaded = true
          }
        })
        .catch(error => {
          this.$notify({
            group: 'poster',
            title: 'Poster',
            text: 'Getting replies required notification amount failed',
            type: 'error'
          })
        })
    },
    setMessageNotificationAmount (amount) {
      if (isNaN(amount)) {
        this.$notify({
          group: 'poster',
          title: 'Poster',
          text: 'Tip amount is not a number',
          type: 'error'
        })
        return
      }
      let hexAmount = parseInt(amount).toString(16)
      if (hexAmount.length % 2) {
        hexAmount = '0' + hexAmount
      }
      const data = ['16Uc2K99dS3VZ9v7mbZSFUh8gJ2432GKiC', '0x21', '0x' + hexAmount]
      let toArray = []
      this.sendTransaction(data, [])
    },
    getMessageNotificationAmount (address) {
      console.log('getMessageNotificationAmount')
      this.posterMessageNotificationAmountLoaded = false
      let findQuery = {}
      findQuery['$and'] = []
      findQuery['$and'].push({'out.b0':{ 'op': 106 }})
      findQuery['$and'].push({'out.s1':'16Uc2K99dS3VZ9v7mbZSFUh8gJ2432GKiC'})
      findQuery['$and'].push({'out.h2':'21'})
      findQuery['$and'].push({'in.e.a': ''  + address + ''})

      let query = {
        'v': 3,
        'q': {
          'find': findQuery,
          'project': { 'out.$': 1 },
          'limit': 1
        },
        'r': {
          'f': '[ .[] | { amount: .out[0].h3 } ]'
        }
      }

      let b64 = Base64.encode(JSON.stringify(query))
      let url = window.bitdbNode + b64

      let header
      if (window.bitdbApiKey) {
        header = {
          headers: { key: window.bitdbApiKey }
        }
      }

      axios
        .get(url, header)
        .then(response => {
          if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
            if (response.data.u !== undefined && response.data.u.length > 0) {
              this.posterMessageNotificationAmount = parseInt(response.data.u[0].amount, 16)
            }
            if ((response.data.u === undefined || response.data.u.length === 0) && response.data.c !== undefined && response.data.c.length > 0) {
              this.posterMessageNotificationAmount = parseInt(response.data.c[0].amount, 16)
            }
            this.posterMessageNotificationAmountLoaded = true
          }
        })
        .catch(error => {
          this.$notify({
            group: 'poster',
            title: 'Poster',
            text: 'Getting messages required notification amount failed',
            type: 'error'
          })
        })
    }
  }
}



