<template>
  	<form @submit.prevent="replyItem(item.txId, content, item.sender, tipAmount)" class="post-form" @click='loginRequired'>
      <p>
        <textarea class="post" name="content" v-model="content" v-on:keydown="calculateContentLenght" placeholder="Post your reply" :maxlength="maxContentCharsCount" required autofocus></textarea>
        <span class="tip-message-input"><input type="number" min="546" autocomplete="off" class="tip-input" v-model="tipAmount" placeholder="Tip"></span>
        <span class="tip-message-input" v-if="repliesNotificationAmount" @click="tipToNotificationAmount()"><i class="fas fa-bell icon"></i> Notify ({{ repliesNotificationAmount }} sat)</span>
        <span class="char-counter" v-if="contentCharsCount > maxContentCharsCount * 0.8">{{ contentCharsCount }}/{{ maxContentCharsCount }}</span>
        <input class="button-active button-message-input" value="Reply" type="submit" @click="txBroadcasting=true" :disabled="txBroadcasting">
      </p>
      <p>&nbsp;</p>
    </form>
</template>


<script>
import { mapActions } from 'vuex'

  export default {
      name: 'PostReplyForm',
      mixins: [],
      components: {
      },
      props: {
        item: Object
      },
      data () {
        return {
          maxContentCharsCount: 184,
          contentCharsCount: 0,
          content: null,
          tipAmount: '',
          repliesNotificationAmount: null
        }
      },
      mounted () {
        let userAddress = window.convertAddress(this.item.sender)
        this.getReplyNotificationAmount(userAddress)
      },
      watch: {
        item: function (val) {
          this.content = ''
        },
        posterReplyNotificationAmountLoaded: function (val) {
          if (val === true && this.posterReplyNotificationAmount !== undefined) {
            this.repliesNotificationAmount = this.posterReplyNotificationAmount
          }
        }
      },
      methods: {
        ...mapActions([
          'addToMyReplies'
        ]),
        calculateContentLenght: function() {
          this.$nextTick(() => {
            if (this.content.length > this.maxContentCharsCount) {
              this.content = this.content.substr(0, this.maxContentCharsCount)
            }
            this.contentCharsCount = this.content.length
          });
        },
        replyItem (txId, content, address, amount) {
          if (this.loginRequired()) {
            return
          }
          this.replyPost(txId, content, address, amount)
          this.addToMyReplies(txId)
          this.content = ''
          this.tipAmount = ''
          this.closeModal('replyModal')
        },
        tipToNotificationAmount () {
          this.tipAmount = this.repliesNotificationAmount
        }
      }
  };
</script>

<style>
</style>



