<template>
  	<form @submit.prevent="newMessage(content)" class="post-form">
      <p>
        <textarea class="post" name="content" v-model="content" v-on:keyup="calculateContentLenght" placeholder="Send your message" :maxlength="maxContentCharsCount" required autofocus></textarea>
        <span class="tip-message-input"><input type="number" min="546" required autocomplete="off" class="tip-input" v-model="tipAmount" placeholder="Tip"></span>
        <span class="tip-message-input" v-if="messagesNotificationAmount" @click="tipToMessageAmount()"><i class="fas fa-bell icon"></i> Notify ({{ messagesNotificationAmount }} sat)</span>
        <span class="char-counter" v-if="contentCharsCount > maxContentCharsCount * 0.8">{{ contentCharsCount }}/{{ maxContentCharsCount }}</span>
        <input class="button-active" value="Send" type="submit" @click="txBroadcasting=true" :disabled="txBroadcasting">
      </p>
      <p>&nbsp;</p>
    </form>
</template>

<script>
  export default {
      name: 'UserMessagesNewForm',
      mixins: [],
      components: {
      },
      props: {
        publicKey: String,
      },
      data () {
        return {
          maxContentCharsCount: 10000,
          contentCharsCount: 0,
          content: null,
          tipAmount: '',
          messagesNotificationAmount: null
        }
      },
      mounted () {
      },
      watch: {
        item: function (val) {
          this.content = ''
        },
        posterMessageNotificationAmountLoaded: function (val) {
          if (val === true && this.posterMessageNotificationAmount !== undefined) {
            this.messagesNotificationAmount = this.posterMessageNotificationAmount
          }
        }
      },
      methods: {
        calculateContentLenght: function() {
          this.$nextTick(() => {
            if (this.content.length > this.maxContentCharsCount) {
              this.content = this.content.substr(0, this.maxContentCharsCount)
            }
            this.contentCharsCount = this.content.length
          });
        },
        newMessage (content) {
          if (this.loginRequired()) {
            return
          }
          this.content = ''
        }
      }
  };
</script>

<style>
</style>



