// for testing
if (navigator.userAgent.indexOf('PhantomJS') > -1) {
  window.localStorage.clear()
}

export const mutations = {
  setUnconfirmedNotificationsChecked (state, unconfirmedNotificationsChecked) {
    state.unconfirmedNotificationsChecked = unconfirmedNotificationsChecked
    localStorage.setItem('unconfirmedNotificationsChecked', unconfirmedNotificationsChecked)
  },
  setNotificationChecked (state, notificationChecked) {
    state.notificationChecked = notificationChecked
    localStorage.setItem('notificationChecked', notificationChecked)
  },
  setUnconfirmedMessagesChecked (state, unconfirmedMessagesChecked) {
    state.unconfirmedMessagesChecked = unconfirmedMessagesChecked
    localStorage.setItem('unconfirmedMessagesChecked', unconfirmedMessagesChecked)
  },
  setMessageChecked (state, messageChecked) {
    state.messageChecked = messageChecked
    localStorage.setItem('messageChecked', messageChecked)
  },
  setLocalPost (state, localPost) {
    state.localPost = localPost
  },
  setCurrentPost (state, currentPost) {
    state.currentPost = currentPost
  },
  setFollowedUsersLocal (state, followedUsersLocal) {
    state.followedUsersLocal = followedUsersLocal
    state.followedUsers = state.followedUsersOnchain.concat(state.followedUsersLocal)
  },
  setFollowedUsersOnchain (state, followedUsersOnchain) {
    state.followedUsersOnchain = followedUsersOnchain
    state.followedUsers = state.followedUsersOnchain.concat(state.followedUsersLocal)
  },
  setFollowedUsers (state, followedUsers) {
    state.followedUsers = followedUsers
  },
  setLoadingFollowers (state, loadingFollowers) {
    state.loadingFollowers = loadingFollowers
  },
  setMutedUsers (state, mutedUsers) {
    state.mutedUsers = mutedUsers
    localStorage.setItem('mutedUsers', JSON.stringify(mutedUsers))
  },
  setTextFilters (state, textFilters) {
    state.textFilters = textFilters
    localStorage.setItem('textFilters', JSON.stringify(textFilters))
  },
  setMyBalance (state, balance) {
    state.myBalance = balance
  },
  setMyTxIds (state, myTxIds) {
    state.myTxIds = myTxIds
  },
  setMyPostLikes (state, myPostLikes) {
    state.myPostLikes = myPostLikes
  },
  setMyPostReplies (state, myPostReplies) {
    state.myPostReplies = myPostReplies
  },
  setProfilePictures (state, profilePictures) {
    state.profilePictures = profilePictures
  },
  setUsernames (state, usernames) {
    state.usernames = usernames
  },
  setParentPosters (state, parentPosters) {
    state.parentPosters = parentPosters
  },
  setActionsCreated (state, actionsCreated) {
    state.actionsCreated = actionsCreated
  },
  setActionsReceived (state, actionsReceived) {
    state.actionsReceived = actionsReceived
  },
  setActionsSent (state, actionsSent) {
    state.actionsSent = actionsSent
  },
  setActionsBalances (state, oldActionsBalances) {
    let actionsBalances = {}
    for (let [key, value] of Object.entries(state.actionsCreated)) {
      if (actionsBalances[key] === undefined) {
        actionsBalances[key] = parseInt(value)
      } else {
        actionsBalances[key] += parseInt(value)
      }
    }
    for (let [key, value] of Object.entries(state.actionsReceived)) {
      if (actionsBalances[key] === undefined) {
        actionsBalances[key] = parseInt(value)
      } else {
        actionsBalances[key] += parseInt(value)
      }
    }
    for (let [key, value] of Object.entries(state.actionsSent)) {
      if (actionsBalances[key] === undefined) {
        actionsBalances[key] = parseInt(value) * (-1)
      } else {
        actionsBalances[key] += parseInt(value) * (-1)
      }
    }
    state.actionsBalances = Object.assign({}, state.actionsBalances, actionsBalances)
  },
  setLikesCount (state, likesCount) {
    state.likesCount = likesCount
  },
  setRepliesCount (state, repliesCount) {
    state.repliesCount = repliesCount
  },
  setCrawledAddresses (state, payload) {
    payload.newCrawledAddresses = payload.newCrawledAddresses.filter( function( el ) {
      return payload.oldCrawledAddresses.indexOf( el ) < 0;
    } )
    state.crawledAddresses = payload.oldCrawledAddresses.concat(payload.newCrawledAddresses)
  },
  setCrawledTxIds (state, payload) {
    payload.newCrawledTxIds = payload.newCrawledTxIds.filter( function( el ) {
      return payload.oldCrawledTxIds.indexOf( el ) < 0;
    } )
    state.crawledTxIds = payload.oldCrawledTxIds.concat(payload.newCrawledTxIds)
  },
  setCrawledParentTxIds (state, payload) {
    payload.newCrawledParentTxIds= payload.newCrawledParentTxIds.filter( function( el ) {
      return payload.oldCrawledParentTxIds.indexOf( el ) < 0;
    } )
    state.crawledParentTxIds = payload.oldCrawledParentTxIds.concat(payload.newCrawledParentTxIds)
  }
}



