<template>
  <div class="grid grey">
    <HeaderNav/>

    <div class="s_left">
      <span v-if="getAddress()">
        <UserNav :address="userAddress" />
      </span>
    </div>

    <div  class="s_results">
      <p class="section-title">
        Shop items
      </p>

<!-- color //-->
        <div class="stream-item">
          <div class="stream-item-header">
            <router-link :to="'/user/' + userLegacyAddress" class="account-group">
              <img class="avatar" v-if="profilePictures[userAddress]" :src="profilePictures[userAddress]" alt="">
              <img class="avatar" v-else src="../../assets/images/profile-picture-normal.png" alt="">
                <span class="full-name-group">
                  <strong class="full-name text-truncate" v-bind:style="{ color: selectedColor}" v-if="usernames[userAddress]">{{ usernames[userAddress] }}</strong>
                  <strong class="full-name text-truncate" v-bind:style="{ color: selectedColor}" v-else>{{ userAddress.substr(0, 12) }}...</strong>
                  <span class="user-badges">
                      <strong class="full-name text-truncate"><i class="fas fa-star following"></i></strong>
                  </span>
                </span>
            </router-link>
            <span>
              <span class="date">&nbsp;- now</span>
            </span>
          </div>
          <div class="stream-item-text-container">
            <p class="item-text">
              <div class="form__field">
                <div class="form__label">
                  <swatches v-model="selectedColor" inline :background-color="'transparent'"></swatches>
                </div>
              </div>
            </p>
          </div>
          <div class="stream-item-footer">
            <span v-if="!confirmColor">
               <span class="post-action-count post-action-price">1000 P</span><i class="fas fa-cart-arrow-down icon" @click="confirmColor = !confirmColor"></i>
            </span>
            <span v-if="confirmColor">
              <span class="post-action-count post-action-price">Confirm payment for <span v-bind:style="{ color: selectedColor}">{{ selectedColor }}</span> (1000 P)</span>
              <span class="post-action-count post-action-price"><a href="javascript:void(0)" @click="purchaseItem(1000, '0000', selectedColor.substr(1))">Yes</a></span>
              <span class="post-action-count post-action-price"><a href="javascript:void(0)" @click="confirmColor = !confirmColor">No</a></span>
            </span>
          </div>
        </div>

        <!-- low price icons //-->
        <div class="stream-item">
          <div class="stream-item-header">
            <router-link :to="'/user/' + userLegacyAddress" class="account-group">
              <img class="avatar" v-if="profilePictures[userAddress]" :src="profilePictures[userAddress]" alt="">
              <img class="avatar" v-else src="../../assets/images/profile-picture-normal.png" alt="">
                <span class="full-name-group">
                  <strong class="full-name text-truncate" v-if="usernames[userAddress]">{{ usernames[userAddress] }}</strong>
                  <strong class="full-name text-truncate" v-else>{{ userAddress.substr(0, 12) }}...</strong>
                  <span class="user-badges">
                      <strong class="full-name text-truncate"><i class="fas fa-star following"></i></strong>
                      <strong class="full-name text-truncate" v-if="iconType1"><i class="following paidBadge" v-bind:class="actionIcons['iconType1.' + iconType1]"></i></strong>
                  </span>
                </span>
            </router-link>
            <span>
              <span class="date">&nbsp;- now</span>
            </span>
          </div>
          <div class="stream-item-text-container">
            <p class="item-text">
              <form>
              <input type="radio" name="iconType1" v-model="iconType1" value="000001" /><i class="fas fa-bicycle"></i>
              <input type="radio" name="iconType1" v-model="iconType1" value="000002" /><i class="fas fa-cat"></i>
              <input type="radio" name="iconType1" v-model="iconType1" value="000003" /><i class="fas fa-dog"></i>
              <input type="radio" name="iconType1" v-model="iconType1" value="000004" /><i class="fas fa-cross"></i>
              <input type="radio" name="iconType1" v-model="iconType1" value="000005" /><i class="fas fa-moon"></i>
              <input type="radio" name="iconType1" v-model="iconType1" value="000006" /><i class="fas fa-pastafarianism"></i>
              </form>
            </p>
          </div>
          <div class="stream-item-footer">
            <span v-if="!confirmIconType1">
               <span class="post-action-count post-action-price">2000 P</span><i class="fas fa-cart-arrow-down icon" @click="confirmIconType1 = !confirmIconType1"></i>
            </span>
            <span v-if="confirmIconType1">
              <span class="post-action-count post-action-price">Confirm payment for <i class="following paidBadge" v-bind:class="actionIcons['iconType1.' + iconType1]"></i> (2000 P)</span>
              <span class="post-action-count post-action-price"><a href="javascript:void(0)" @click="purchaseItem(2000, '0001', iconType1)">Yes</a></span>
              <span class="post-action-count post-action-price"><a href="javascript:void(0)" @click="confirmIconType1 = !confirmIconType1">No</a></span>
            </span>
          </div>
        </div>

        <!-- high price icons //-->
        <div class="stream-item">
          <div class="stream-item-header">
            <router-link :to="'/user/' + userLegacyAddress" class="account-group">
              <img class="avatar" v-if="profilePictures[userAddress]" :src="profilePictures[userAddress]" alt="">
              <img class="avatar" v-else src="../../assets/images/profile-picture-normal.png" alt="">
                <span class="full-name-group">
                  <strong class="full-name text-truncate" v-if="usernames[userAddress]">{{ usernames[userAddress] }}</strong>
                  <strong class="full-name text-truncate" v-else>{{ userAddress.substr(0, 12) }}...</strong>
                  <span class="user-badges">
                      <strong class="full-name text-truncate"><i class="fas fa-star following"></i></strong>
                      <strong class="full-name text-truncate" v-if="iconType2"><i class="following paidBadge" v-bind:class="actionIcons['iconType2.' + iconType2]"></i></strong>
                  </span>
                </span>
            </router-link>
            <span>
              <span class="date">&nbsp;- now</span>
            </span>
          </div>
          <div class="stream-item-text-container">
            <p class="item-text">
              <form>
              <input type="radio" name="iconType2" v-model="iconType2" value="000001" /><i class="fas fa-chess-king"></i>
              <input type="radio" name="iconType2" v-model="iconType2" value="000002" /><i class="fas fa-chess-queen"></i>
              <input type="radio" name="iconType2" v-model="iconType2" value="000003" /><i class="fas fa-bolt"></i>
              <input type="radio" name="iconType2" v-model="iconType2" value="000004" /><i class="fas fa-dragon"></i>
              </form>
            </p>
          </div>
          <div class="stream-item-footer">
            <span v-if="!confirmIconType2">
               <span class="post-action-count post-action-price">10000 P</span><i class="fas fa-cart-arrow-down icon" @click="confirmIconType2 = !confirmIconType2"></i>
            </span>
            <span v-if="confirmIconType2">
              <span class="post-action-count post-action-price">Confirm payment for <i class="following paidBadge" v-bind:class="actionIcons['iconType2.' + iconType2]"></i> (10000 P)</span>
              <span class="post-action-count post-action-price"><a href="javascript:void(0)" @click="purchaseItem(10000, '0002', iconType2)">Yes</a></span>
              <span class="post-action-count post-action-price"><a href="javascript:void(0)" @click="confirmIconType2 = !confirmIconType2">No</a></span>
            </span>
          </div>
        </div>

    </div>

    <div class="s_right topics">
    </div>

    <Footer/>
  </div>
</template>

<script>
import Swatches from 'vue-swatches'
import "vue-swatches/dist/vue-swatches.min.css"

import { mapState, mapActions } from 'vuex'
import HeaderNav from '@/components/Header/Nav'
import Footer from '@/components/Footer'
import UserNav from '@/components/User/Nav'

export default {
  name: 'ShopIndex',
  mixins: [],
  props: {
  },
  data () {
    return {
      userAddress: window.convertAddress(this.getAddress()),
      userLegacyAddress: window.convertAddress(this.getAddress(), 'legacy'),
      selectedColor: null,
      iconType1: null,
      iconType2: null,
      confirmColor: null,
      confirmIconType1: false,
      confirmIconType2: false
    }
  },
  head: function () {
    return {
      title: {
        inner: 'Shop digital and physical items'
      },
      // Meta tags
      meta: [
        { name: 'application-name', content: 'Poster.cash' },
        { name: 'description', content: 'Shop digital and physical items on Poster.cash. Enhance your profile, spice your poster personality', id: 'desc' },
        { name: 'twitter:title', content: 'Shop digital and physical items' },
        { n: 'twitter:description', c: 'Shop digital and physical items on Poster.cash. Enhance your profile, spice your poster personality' },
        { itemprop: 'name', content: 'Shop digital and physical items' },
        { itemprop: 'description', content: 'Shop digital and physical items on Poster.cash. Enhance your profile, spice your poster personality' },
        { property: 'og:title', content: 'Shop digital and physical items' },
      ]
    }
  },
  components: {
    Swatches,
    HeaderNav,
    Footer,
    UserNav
  },
  mounted () {
    this.loginRequired()
    if (this.getAddress()) {
      this.addNewUsersData({ 'address': this.getAddress(), 'newTxIds': [], 'newAddresses': [] })
    }
  },
  created() {
  },
  computed: {
    ...mapState([
      'followedUsers',
      'profilePictures',
      'usernames'
    ])
  },
  watch: {
  },
  methods: {
    ...mapActions([
      'addNewUsersData'
    ]),
    purchaseItem (amount, itemType, itemNumber) {
      this.sendActionCoins(window.posterBitcoinAddress, amount, itemType, itemNumber)
    }
  }
}
</script>

<style>
.post-action-price {
  margin-left: 0px;
  margin-right: 5px;
}
</style>



