<template>
  <div class="form">
  	<form @submit.prevent="setGeneralSettings">
    <h2>General settings</h2>
    <div class="field-block">
      <h4>External resourses</h4>
      <input type="checkbox" name="postShowImages" value="true" v-model="postShowImages" title="Show external images"/> Show external images
      <input type="checkbox" name="postShowVideos" value="true" v-model="postShowVideos" title="Show external videos"/> Show external videos
    </div>
    <div class="form-buttons">
      <input value="Submit" type="submit">
    </div>
    </form>

  	<form @submit.prevent="setNotificationsSettings">
    <h2>Notifications settings</h2>
    <div class="field-block">
      <h4>Notify on new</h4>
      <input type="checkbox" name="notifyOnLike" value="true" v-model="notifyOnLike" title="Notify on new likes"/> Likes
      <input type="checkbox" name="notifyOnFollow" value="true" v-model="notifyOnFollow" title="Notify on new follows"/> Follows
    </div>
    <div class="field-block">
      <h4>Notify about reply only if tipped</h4>
  	   <input type="number" name="repliesNotificationAmount" v-model="repliesNotificationAmount" title="Amount (satoshis)" placeholder="Amount (satoshis)" @click='loginRequired'/>
    </div>
    <div class="field-block">
      <h4>Notify about message only if tipped</h4>
  	   <input type="number" name="messagesNotificationAmount" v-model="messagesNotificationAmount" title="Amount (satoshis)" placeholder="Amount (satoshis)" @click='loginRequired'/>
    </div>
    <div class="form-buttons">
      <input value="Submit" type="submit">
    </div>
    </form>

  	<form @submit.prevent="addFilter">
    <h2>Filters</h2>
    <div class="field-block">
      <h4>Filter word or phrase</h4>
  	   <input type="text" name="textFilter" v-model="textFilter" title="Text Filter" placeholder="Word to filter"/>
    </div>
    <div class="field-block">
      <h4>Add regular expression filter</h4>
  	   <input type="text" name="regexFilter" v-model="regexFilter" title="regex Filter" placeholder="Regular expression filter"/>
    </div>
    <div class="form-buttons">
      <input value="Add filter" type="submit">
    </div>
    <div class="field-block" v-if="textFilters.length > 0">
      <h4>
        My filters
      </h4>
      <p style="color:#911;font-size:small" v-if="textFilters.length < 3">
        Use 3 or more filters for better performance
      </p>
      <div class="topic-name" v-for="(item, index) in textFilters">
        {{ item }} <a href="javascript:void(0);" @click="removeFilter(item)">[x]</a>
      </div>
    </div>
    </form>

  	<form @submit.prevent="setValues">
    <h2>Bitdb settings</h2>
    <div class="field-block">
      <h4>Bitdb node</h4>
  	   <input type="text" name="bitdbNode" v-model="bitdbNode" title="Bitdb node" placeholder="Bitdb node address"/>
    </div>
    <div class="field-block">
      <h4>Bitdb API key</h4>
      <input type="text" name="bitdbApiKey" v-model="bitdbApiKey" title="Bitdb API key" placeholder="Bitdb API key"/>
    </div>
    <div class="field-block">
      <h4>Bitdb address format</h4>
      <input type="radio" name="bitdbAddressFormat" value="legacy" v-model="bitdbAddressFormat" title="Bitdb Address Format" placeholder="Bitdb Address Format"/> Bitcoin Address (1G....)
      <input type="radio" name="bitdbAddressFormat" value="cashaddr" v-model="bitdbAddressFormat" title="Bitdb Address Format" placeholder="Bitdb Address Format"/> cashaddr (qp...)
    </div>
    <div class="field-block">
      <h4>Block explorer RPC</h4>
      <input type="text" name="blockExplorerRpc" v-model="blockExplorerRpc" title="Block explorer RPC" placeholder="Block explorer RPC"/>
    </div>
    <div class="field-block">
      <h4>Bitsocket node</h4>
  	   <input type="text" name="bitsocketNode" v-model="bitsocketNode" title="Bitsocket node" placeholder="Bitsocket node address"/>
    </div>
    <div class="form-buttons">
      <input value="submit" type="submit">
      <p style="color:#666;font-size:small">Saved settings are stored only in your browser.</p>
    </div>
  </form>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'

  export default {
    name: 'Settings',

    data () {
      return {
        postShowImages: null,
        postShowVideos: null,
        notifyOnLike: null,
        notifyOnFollow: null,
        repliesNotificationAmount: null,
        oldRepliesNotificationAmount: null,
        messagesNotificationAmount: null,
        oldMessagesNotificationAmount: null,
        textFilter: null,
        regexFilter: null,
        bitdbNode: null,
        bitdbApiKey: null,
        bitdbAddressFormat: null,
        bitsocketNode: null,
        blockExplorerRpc: null,
        userAddress: null
      }
    },
    components: {
    },
    mounted () {
      this.postShowImages = window.postShowImages
      this.postShowVideos = window.postShowVideos
      this.notifyOnLike = window.notifyOnLike
      this.notifyOnFollow = window.notifyOnFollow
      this.bitdbNode = window.bitdbNode
      this.bitdbApiKey = window.bitdbApiKey
      this.bitdbAddressFormat = window.bitdbAddressFormat
      this.blockExplorerRpc = window.blockExplorerRpc
      this.bitsocketNode = window.bitsocketNode

      this.userAddress = window.convertAddress(this.getAddress())
      this.getReplyNotificationAmount(this.userAddress)
      this.getMessageNotificationAmount(this.userAddress)
    },
    computed: {
      ...mapState([
        'textFilters'
      ])
    },
    watch: {
      posterReplyNotificationAmountLoaded: function (val) {
        if (val === true && this.posterReplyNotificationAmount !== undefined) {
          this.oldRepliesNotificationAmount = this.posterReplyNotificationAmount
          this.repliesNotificationAmount = this.posterReplyNotificationAmount
        }
      },
      posterMessageNotificationAmountLoaded: function (val) {
        if (val === true && this.posterMessageNotificationAmount !== undefined) {
          this.oldMessagesNotificationAmount = this.posterMessageNotificationAmount
          this.messagesNotificationAmount = this.posterMessageNotificationAmount
        }
      },
    },
    methods: {
      ...mapActions([
        'addTextFilter',
        'removeTextFilter'
      ]),
      addFilter () {
        if (this.textFilter) {
          let filter = ''
          filter = '^((?!' + this.textFilter + ').)*$'
          this.addTextFilter(filter)
        }
        //  ^((?![0-9a-f]{8,217}).)*$
        if (this.regexFilter) {
          this.addTextFilter(this.regexFilter)
        }
        this.textFilter = ''
        this.regexFilter = ''

        this.$notify({
          group: 'poster',
          title: 'Settings',
          text: 'Filter added',
          type: 'success'
        })
      },
      removeFilter (item) {
        this.removeTextFilter(item)

        this.$notify({
          group: 'poster',
          title: 'Settings',
          text: 'Filter removed',
          type: 'success'
        })
      },
      setGeneralSettings () {
        if (this.postShowImages === false && this.postShowImages != window.postShowImagesDefault) {
          localStorage.setItem('postShowImages', this.postShowImages)
          window.postShowImages = this.postShowImages
        } else {
          localStorage.removeItem('postShowImages')
          window.postShowImages = window.postShowImagesDefault
        }

        if (this.postShowVideos === false && this.postShowVideos != window.postShowVideosDefault) {
          localStorage.setItem('postShowVideos', this.postShowVideos)
          window.postShowVideos = this.postShowVideos
          window.postShowVideosEmbed = 'false'
        } else {
          localStorage.removeItem('postShowVideos')
          window.postShowVideos = window.postShowVideosDefault
          window.postShowVideosEmbed = 'youtube, vimeo'
        }

        this.$notify({
          group: 'poster',
          title: 'Settings',
          text: 'General settings changed',
          type: 'success'
        })
      },
      setNotificationsSettings () {
        if (this.notifyOnLike === false && this.notifyOnLike != window.notifyOnLikeDefault) {
          localStorage.setItem('notifyOnLike', this.notifyOnLike)
          window.notifyOnLike = this.notifyOnLike
        } else {
          localStorage.removeItem('notifyOnLike')
          window.notifyOnLike = window.notifyOnLikeDefault
        }
        if (this.notifyOnFollow === false && this.notifyOnFollow != window.notifyOnFollowDefault) {
          localStorage.setItem('notifyOnFollow', this.notifyOnFollow)
          window.notifyOnFollow = this.notifyOnFollow
        } else {
          localStorage.removeItem('notifyOnFollow')
          window.notifyOnFollow = window.notifyOnFollowDefault
        }

        if (this.repliesNotificationAmount !== null && !(Number.isNaN(this.repliesNotificationAmount)) && parseInt(this.oldRepliesNotificationAmount) !== parseInt(this.repliesNotificationAmount)) {
          this.setReplyNotificationAmount(parseInt(this.repliesNotificationAmount))
        }
        if (this.messagesNotificationAmount !== null && !(Number.isNaN(this.messagesNotificationAmount)) && parseInt(this.oldMessagesNotificationAmount) !== parseInt(this.messagesNotificationAmount)) {
          this.setMessageNotificationAmount(parseInt(this.messagesNotificationAmount))
        }

        this.$notify({
          group: 'poster',
          title: 'Settings',
          text: 'Notifications settings changed',
          type: 'success'
        })
      },
      setValues () {
        if (this.bitdbNode && this.bitdbNode !== window.bitdbNodeDefault) {
          if (this.bitdbNode === window.bitdbBCHNodeDefault) {
            localStorage.setItem('networkName', 'BCH')
            window.networkName = 'BCH'
          } else {
            localStorage.setItem('networkName', 'Custom')
            window.networkName = 'Custom'
          }
          localStorage.setItem('bitdbNode', this.bitdbNode)
          window.bitdbNode = this.bitdbNode
        } else {
          localStorage.removeItem('networkName')
          localStorage.removeItem('bitdbNode')
          window.networkName = window.networkNameDefault
          window.bitdbNode = window.bitdbNodeDefault
        }

        if (this.bitdbApiKey && this.bitdbApiKey != window.bitdbApiKeyDefault) {
          localStorage.setItem('bitdbApiKey', this.bitdbApiKey)
          window.bitdbApiKey = this.bitdbApiKey
        } else {
          localStorage.removeItem('bitdbApiKey')
          window.bitdbApiKey = window.bitdbApiKeyDefault
        }

        if (this.bitdbAddressFormat && this.bitdbAddressFormat != window.bitdbAddressFormatDefault) {
          localStorage.setItem('bitdbAddressFormat', this.bitdbAddressFormat)
          window.bitdbAddressFormat = this.bitdbAddressFormat
        } else {
          localStorage.removeItem('bitdbAddressFormat')
          window.bitdbAddressFormat = window.bitdbAddressFormatDefault
        }

        if (this.blockExplorerRpc && this.blockExplorerRpc != window.blockExplorerRpcDefault) {
          localStorage.setItem('blockExplorerRpc', this.blockExplorerRpc)
          window.blockExplorerRpc = this.blockExplorerRpc
        } else {
          localStorage.removeItem('blockExplorerRpc')
          window.blockExplorerRpc = window.blockExplorerRpcDefault
        }

        if (this.bitsocketNode && this.bitsocketNode != window.bitsocketNodeDefault) {
          localStorage.setItem('bitsocketNode', this.bitsocketNode)
          window.bitsocketNode = this.bitsocketNode
        } else {
          localStorage.removeItem('bitsocketNode')
          window.bitsocketNode = window.bitsocketNodeDefault
        }

        this.$notify({
          group: 'poster',
          title: 'Settings',
          text: 'Bitdb settings changed',
          type: 'success'
        })
      }
    }
  };
</script>

<style>
</style>



