<template>
  <span>
        <li v-if="logged" style="float: right;"><a href="javascript:void(0)" @click="openModal('newModal')" class="button-active">Post</a></li>
     		<li v-if="logged" class="dropdown" style="float: right;">
          <i class="fas fa-user-circle user-nav-icon" @click="showProfileNav = !showProfileNav"></i>
            <ul v-bind:class="{ 'nav-dropdown': !showProfileNav, 'nav-dropdown-active': showProfileNav }">
              <li><a href="javascript:void(0)">Balance: {{ balance }} sat</a></li>
              <li class="mobileOnlyNav"><a href="javascript:void(0)">Notifications: {{ notificationsCount }}</a></li>
              <li class="mobileOnlyNav"><a href="javascript:void(0)">Messages: {{ notificationsCount }}</a></li>
              <li><router-link to="/pages/editProfile">Edit profile</router-link></li>
              <li><router-link to="/wallet/receive">Receive {{ networkName || 'BSV' }}</router-link></li>
              <li><router-link to="/wallet/send">Send {{ networkName || 'BSV' }}</router-link></li>
              <li><router-link to="/wallet/export">Export wallet key</router-link></li>
              <li><a href="/" @click="logout">Logout</a></li>
            </ul>
        </li>
     		<li v-if="logged" style="float: right;">
          <router-link :to="'/user/notifications/' + userLegacyAddress" class="nav-icon"><i class="fas fa-bell user-nav-icon" v-bind:class="{ 'user-nav-icon-active': notificationsCount }"><span class="badge" v-if="notificationsCount">{{ notificationsCount }}</span></i></router-link>
        </li>
<!--
     		<li v-if="logged" style="float: right;">
          <router-link :to="'/shop'" class="nav-icon"><i class="fas fa-shopping-cart user-nav-icon"></i></router-link>
        </li>
// -->
     		<li v-else style="float: right;"><a href="javascript:void(0)" @click="openModal('loginModal')" class="button-active">Sign in</a></li>
  </span>
</template>


<script>
import { mapState, mapActions } from 'vuex'

  export default {
      name: 'UserNav',
      mixins: [],
      data () {
        return {
          userLegacyAddress: window.convertAddress(this.getAddress(), 'legacy'),
          userAddress: window.convertAddress(this.getAddress()),
          networkName: window.networkName,
          showProfileNav: false,
          notificationsCount: 0,
          messagesCount: 0
        }
      },
      props: {
      },
      mounted () {
        if (this.logged) {
          this.getReplyNotificationAmount(this.userAddress)
          this.getMessageNotificationAmount(this.userAddress)
          this.getAddressInfo()
        }
      },
      computed: {
        ...mapState([
          'notificationChecked',
          'unconfirmedNotificationsChecked',
          'messageChecked',
          'unconfirmedMessagesChecked',
          'myTxIds'
        ]),
        logged: function () {
          if (this.loggedViewOnlyMode() || this.loggedViewMode() || this.loggedSpendMode()) {
            return true
          }
          return false
        },
        balance: function () {
          if (this.addressInfo) {
            return this.addressInfo.balanceSat
          }
          return 0
        }
      },
      watch: {
        posterReplyNotificationAmountLoaded: function (val) {
          if (val === true && this.myTxIds.length > 0) {
            this.getNotificationsCount()
          }
        },
        posterMessageNotificationAmountLoaded: function (val) {
          if (val === true) {
            this.getMessagesCount()
          }
        },
        myTxIds: function (val) {
          if (val.length > 0 && this.posterReplyNotificationAmountLoaded === true) {
            this.getNotificationsCount()
          }
        }
      },
      methods: {
        getNotificationsCount () {
          if (this.myTxIds.length > 0) {
            const bytes = bs58.decode(window.convertAddress(this.getAddress(), 'legacy'))
            const userAddressHex = bytes.toString('hex').substr(2,40)

            let txIdsArray = []
            for (let i = 0; i < this.myTxIds.length; ++i) {
              txIdsArray.push(reverseHex(this.myTxIds[i]))
            }

            let opReturnQuery = {}
            opReturnQuery['$or'] = []

            let txIdsQuery = {}
            txIdsQuery['$or'] = []
            // 'like' query
            if (window.notifyOnLike === true) {
              let likeQuery = {}
              likeQuery['out.h1'] = '6d04'
              txIdsQuery['$or'].push(likeQuery)
            }
            if (!this.posterReplyNotificationAmount) {
              // 'reply' query (free)
              let replyQuery = {}
              replyQuery['out.h1'] = '6d03'
              txIdsQuery['$or'].push(replyQuery)
            } else {
              // 'reply' query (paid)
              let replyQuery = {}
              replyQuery['$and'] = []
              replyQuery['$and'].push({'out.h1': '6d03'})
              replyQuery['$and'].push({'in.e.a': {'$ne': this.userAddress}})
              replyQuery['$and'].push({'out': {'$elemMatch': {'e.a': {'$eq': this.userAddress}, 'e.v': {'$gte': parseInt(this.posterReplyNotificationAmount)}}}})
              opReturnQuery['$or'].push(replyQuery)
            }

            if (window.notifyOnLike === true || !this.posterReplyNotificationAmount) {
              opReturnQuery['$or'].push({
                '$and': [
                  txIdsQuery,
                  {'out.h2': {'$in': txIdsArray}}
                ]
              })
            }

            // 'follow' query
            if (window.notifyOnFollow === true) {
              let followQuery = {
                '$and': [
                  {'out.h1': {'$in': ['6d06']}},
                  {
                    'out.h2': '' + userAddressHex + ''
                  },
                  {
                    'in.e.a': {'$ne': this.userAddress}
                  }
                ]
              }
              opReturnQuery['$or'].push(followQuery)
            }

            let query = {
            	'v': 3,
            	'q': {
            		'aggregate': [{
            				'$match': {
            					'$and': [{
            							'$or': [{
            									'blk.t': null
            								},
            								{
            									'blk.t': {
            										'$gte': this.notificationChecked
            									}
            								}
            							]
            						},
            						{
            							'out.b0': {
            								'op': 106
            							}
            						},
                        opReturnQuery
            					]
            				}
            			},
            			{
            				'$group': {
            					'_id': null,
            					'count': {
            						'$sum': 1
            					}
            				}
            			}
            		]
            	}
            }

            let b64 = Base64.encode(JSON.stringify(query))
            let url = window.bitdbNode + b64

            // temporary fix, remove when poster planaria is active
            if (this.posterReplyNotificationAmount && window.networkName === 'BSV') {
              url = window.bitdbNodeSVGenesisDefault + b64
            }

            let header
            if (window.bitdbApiKey) {
              header = {
                headers: { key: window.bitdbApiKey }
              }
            }

            axios
              .get(url, header)
              .then(response => {
                this.notificationsCount = 0
                if (response.data.u.length > 0) {
                  this.notificationsCount += response.data.u[0].count
                }
                if (response.data.c.length > 0) {
                  this.notificationsCount += response.data.c[0].count
                }
                this.notificationsCount -= this.unconfirmedNotificationsChecked
                if (this.notificationsCount < 0) {
                  this.notificationsCount = 0
                }
              })
              .catch(error => {
                console.log(JSON.stringify(error))
              })
          }
        },
        getMessagesCount () {
          let opReturnQuery = {}
          opReturnQuery['$or'] = []

            let messageQuery = {}
            messageQuery['$and'] = []
            messageQuery['$and'].push({'out.s1': '16Uc2K99dS3VZ9v7mbZSFUh8gJ2432GKiC'})
            messageQuery['$and'].push({'out.h1': '01'})
            messageQuery['$and'].push({'out.s3': this.userAddress})
            if (!this.posterMessageNotificationAmount) {
              // paid
              messageQuery['$and'].push({'out': {'$elemMatch': {'e.a': {'$eq': this.userAddress}, 'e.v': {'$gte': parseInt(this.posterMessageNotificationAmount)}}}})
            }
            opReturnQuery['$or'].push(messageQuery)

            let query = {
            	'v': 3,
            	'q': {
            		'aggregate': [{
            				'$match': {
            					'$and': [{
            							'$or': [{
            									'blk.t': null
            								},
            								{
            									'blk.t': {
            										'$gte': this.messageChecked
            									}
            								}
            							]
            						},
            						{
            							'out.b0': {
            								'op': 106
            							}
            						},
                        opReturnQuery
            					]
            				}
            			},
            			{
            				'$group': {
            					'_id': null,
            					'count': {
            						'$sum': 1
            					}
            				}
            			}
            		]
            	}
            }

            let b64 = Base64.encode(JSON.stringify(query))
            let url = window.bitdbNode + b64

            let header
            if (window.bitdbApiKey) {
              header = {
                headers: { key: window.bitdbApiKey }
              }
            }

            axios
              .get(url, header)
              .then(response => {
                this.messagesCount = 0
                if (response.data.u.length > 0) {
                  this.messagesCount += response.data.u[0].count
                }
                if (response.data.c.length > 0) {
                  this.messagesCount += response.data.c[0].count
                }
                this.messagesCount -= this.unconfirmedMessagesChecked
                if (this.messagesCount < 0) {
                  this.messagesCount = 0
                }
              })
              .catch(error => {
                console.log(JSON.stringify(error))
              })
        }
      }
  };
</script>

<style>
.user-nav-icon {
  margin-top:10px;
  margin-right: 10px;
  font-size: 32px;
  text-decoration: none;
  color: #657786;
  cursor: pointer;
}
.user-nav-icon-active {
  color: #f57786;
}
.user-nav-icon .badge {
  display: block;
  position: absolute;
  top: 0px;
  right: 3px;
  font-size: 11px;
  line-height: 16px;
  height: 16px;
  padding: 0 5px;
  font-family: Arial, sans-serif;
  color: white;
  background-color: red;
  text-shadow: 0 1px rgba(0, 0, 0, 0.25);
  border: 1px solid;
  border-radius: 10px;
  -webkit-box-shadow: none;
  box-shadow: none;
}
.mobileOnlyNav {
  display: none;
}
@media screen and (max-width: 800px) {
	.user-nav-icon {
	  display: none;
	}
  .nav-dropdown {
    display: block;
    margin: 0;
  }
  .nav-dropdown-active {
    display: block;
    margin: 0;
  }
  .button-active {
    display: block;
    margin: 0;
  }
  .mobileOnlyNav {
    display: block;
  }
}
</style>



