<template>
  <div class="grid grey">
    <HeaderNav/>

    <div class="s_left">
      <span v-if="myAddress">
        <UserNav :address="myAddress" />
      </span>
    </div>

    <div  class="s_results"  v-infinite-scroll="loadMore" infinite-scroll-disabled="scrollLoading" infinite-scroll-distance="500">
    <div v-if="loading === false">

        <PostNewForm/>

        <PostListItem
          v-for="(item, index) in resultBitdbItems"
          :key="index"
          :item="item"
          :topics="followedTopics">
        </PostListItem>
        <div class="n_results" v-bind:class="{ 'preloader': !streamEnd }"></div>
    </div>
    <div class="s_results" v-else>
      <div class="n_results" v-bind:class="{ 'preloader': !streamEnd }"></div>
    </div>
  </div>

    <div class="s_right topics">
      <div class="topic-title">Followed topics</div>
      <p style="color:#911;font-size:small" v-if="followedTopics.length < 3">
        Follow 3 or more topics for better performance
      </p>
      <div class="topic-name" v-for="(item, index) in followedTopics">
        {{ item }} <a href="javascript:void(0);" @click="removeTopic(index)">[x]</a>
      </div>

      <form class="ui add topic-form" @submit.prevent="addTopic">
        <div class="field-block">
          <input class="topic-input" type="text" name="newTopic" v-model="newTopic" placeholder="Follow topic" required autocomplete="off">
        </div>
        <div class="form-buttons">
          <input class="button" value="Follow" type="submit">
        </div>
      </form>
    </div>

    <Footer/>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import HeaderNav from '@/components/Header/Nav'
import Footer from '@/components/Footer'
import UserNav from '@/components/User/Nav'
import PostListItem from '@/components/Post/ListItem'
import PostNewForm from '@/components/Post/NewForm'

export default {
  name: 'Home',
  mixins: [],
  props: {
  },
  data () {
    return {
      myAddress: this.getAddress() ? window.convertAddress(this.getAddress()) : null,
      item: null,
      followedTopics: [],
      newTopic: null,
      filters: [],
      newFilter: null,
      newPostContent: null,
      findQuery: null,
      offset: 0,
      page: 1,
      itemsPerPage: 10,
      resultBitsocketItems: [],
      resultBitdbItems: [],
      loading: false,
      errored: false,
      streamEnd: false,
      scrollLoading: false
    }
  },
  head: function () {
    return {
      title: {
        inner: 'Poster. Bitcoin Cash made social'
      },
      // Meta tags
      meta: [
        { name: 'application-name', content: 'Poster.cash' },
        { name: 'description', content: 'Poster is client for the Memo protocol. Get the latest news live streight out of the Bitcoin blockchain.', id: 'desc' },
        { name: 'twitter:title', content: 'Poster. Bitcoin Cash made social' },
        { n: 'twitter:description', c: 'Poster is client for the Memo protocol. Get the latest news live streight out of the Bitcoin blockchain.'},
        { itemprop: 'name', content: 'Poster. Bitcoin Cash made social' },
        { itemprop: 'description', content: 'Poster is client for the Memo protocol. Get the latest news live streight out of the Bitcoin blockchain.' },
        { property: 'og:title', content: 'Poster. Bitcoin Cash made social' },
      ]
    }
  },
  components: {
    HeaderNav,
    Footer,
    UserNav,
    PostListItem,
    PostNewForm
  },
  mounted () {
    this.$notify({
      group: 'foo',
      title: 'Important message',
      text: 'Hello user! This is a notification!'
    });

    if (localStorage.getItem('followedTopics')) {
      this.followedTopics = JSON.parse(localStorage.getItem('followedTopics'))
    }
    this.page = parseInt(this.offset/this.itemsPerPage) + 1
  },
  created() {
  },
  computed: {
    ...mapState([
      'localPost',
      'loadingFollowers',
      'followedUsers',
      'mutedUsers',
      'myPostLikes',
      'myPostReplies',
      'profilePictures',
      'usernames',
      'likesCount',
      'repliesCount',
      'textFilters'
    ])
  },
  watch: {
    localPost: function (val) {
      if (this.loading === false && this.loadingFollowers === false && this.localPost !== null) {
        this.resultBitdbItems.unshift(this.localPost)
        this.changeLocalPost(null)
      }
    },
    mutedUsers: function (val) {
      if (this.loading === false && this.loadingFollowers === false) {
        this.resultBitdbItems = []
        this.calculateQuery()
        this.searchTheBlockchainForPosts()
      }
    },
    followedUsers: function (val) {
      if (this.loading === false && this.loadingFollowers === false) {
        this.resultBitdbItems = []
        this.calculateQuery()
        this.searchTheBlockchainForPosts()
      }
    },
    followedTopics: function (val) {
      if (this.loading === false && this.loadingFollowers === false) {
        this.resultBitdbItems = []
        this.calculateQuery()
        this.searchTheBlockchainForPosts()
      }
    }
  },
  methods: {
    ...mapActions([
      'addNewUsersData',
      'changeLocalPost'
    ]),
    addTopic () {
      if (this.newTopic && !this.followedTopics.includes(this.newTopic)) {
        this.followedTopics.push(this.newTopic)
        localStorage.setItem('followedTopics', JSON.stringify(this.followedTopics))
        this.newTopic = ''

        this.$notify({
          group: 'poster',
          title: 'Topics',
          text: 'Topic followed',
          type: 'success'
        })
      }
    },
    removeTopic (index) {
      this.followedTopics.splice(index, 1)
      localStorage.setItem('followedTopics', JSON.stringify(this.followedTopics))

      this.$notify({
        group: 'poster',
        title: 'Topics',
        text: 'Topic removed',
        type: 'success'
      })
    },
    calculateQuery () {
      let query = {}
      query['$or'] = []
      let followedQuery = {}
      followedQuery['$and'] = []
      followedQuery['$and'].push({'out.b0':{ 'op': 106 }})
      followedQuery['$and'].push({'out.h1': {'$in': ['6d02', '6d03', '6d0c']}})

      // using $regex
      let followedSearch = {}
      followedSearch['$or'] = []
      for (let i = 0; i < this.followedTopics.length; ++i) {
        followedSearch['$or'].push({'out.s2': { '$regex': '\\b' + this.followedTopics[i] + '\\b', '$options': 'ui' }})
        followedSearch['$or'].push({'out.s3': { '$regex': '\\b' + this.followedTopics[i] + '\\b', '$options': 'ui' }})
      }

      let followedUsersArray = []
      for (let i = 0; i < this.followedUsers.length; ++i) {
        followedUsersArray.push(this.followedUsers[i])
      }
      followedUsersArray.push(window.convertAddress(this.getAddress()))
      followedSearch['$or'].push({'in.e.a': {'$in': followedUsersArray}})

      followedQuery['$and'].push(followedSearch)

      for (let i = 0; i < this.mutedUsers.length; ++i) {
        followedQuery['$and'].push({ 'in.e.a': { '$ne': window.convertAddress(this.mutedUsers[i]) } })
      }

      for (let i = 0; i < this.textFilters.length; ++i) {
        let followedFilterS2 = {}
        followedFilterS2['$or'] = []
        followedFilterS2['$or'].push({'out.s2': { '$regex': this.textFilters[i], '$options': 'is' }})
        followedQuery['$and'].push(followedFilterS2)

        let followedFilterS3 = {}
        followedFilterS3['$or'] = []
        followedFilterS3['$or'].push({'out.s3': { '$exists': false }})
        followedFilterS3['$or'].push({'out.s3': { '$regex': this.textFilters[i], '$options': 'is' }})
        followedQuery['$and'].push(followedFilterS3)
      }

      query['$or'].push(followedQuery)
      this.findQuery = query
    },
    searchTheBlockchainForPosts (offset = 0) {
      if (offset === 0) {
        this.loading = true
      }
      this.errored = false

      if (!this.findQuery) {
        return
      }
      let query = {
        'v': 3,
        'q': {
          'find': this.findQuery,
          'project': { 'in': 1, 'out.$': 1, 'blk': 1, 'tx': 1 },
          'skip': parseInt(offset),
          'limit': this.itemsPerPage
        },
        'r': {
          'f': '[ .[] | {prefix: .out[0].h1, sender: .in[0].e.a, parentTxId: .out[0].h2, content1: .out[0].s2, content2: .out[0].s3, timestamp: .blk.t?, txId: .tx.h} ]'
        }
      }

      let b64 = Base64.encode(JSON.stringify(query))
      let url = window.bitdbNode + b64

      let header
      if (window.bitdbApiKey) {
        header = {
          headers: { key: window.bitdbApiKey }
        }
      }

      axios
        .get(url, header)
        .then(response => {
          if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
            let newBitdbItems = []
            if (response.data.u !== undefined) {
              newBitdbItems = newBitdbItems.concat(response.data.u.reverse())
            }
            if (response.data.c !== undefined) {
              newBitdbItems = newBitdbItems.concat(response.data.c)
            }

            // populate usernames and txIds
            let newAddresses = []
            let newTxIds = []
            for (let i = 0; i < newBitdbItems.length; ++i) {
              if(newAddresses.indexOf(newBitdbItems[i].sender) === -1) {
                newAddresses.push(newBitdbItems[i].sender)
              }
              if(newTxIds.indexOf(newBitdbItems[i].txId) === -1) {
                newTxIds.push(newBitdbItems[i].txId)
              }
            }
            this.addNewUsersData({ 'address': this.getAddress(), 'newTxIds': newTxIds, 'newAddresses': [...newAddresses] })
            this.resultBitdbItems = this.resultBitdbItems.concat(newBitdbItems)

            if (response.data.c.length < this.itemsPerPage) {
              this.streamEnd = true
            }
          }
          this.offset += this.itemsPerPage
          this.loading = false
          this.scrollLoading = false
        })
        .catch(error => {
          this.errored = true
          this.loading = false
        })
    },
    loadMore() {
      this.scrollLoading = true;
      if (this.streamEnd === false && this.errored === false && this.loading === false && this.loadingFollowers === false) {
        this.searchTheBlockchainForPosts(this.offset)
      }
    }
  }
}
</script>

<style>
</style>



