<template>
  	<form @submit.prevent="newItem(content)" class="post-form" @click='loginRequired'>
      <p>
        <textarea class="post" name="content" v-model="content" v-on:keydown="calculateContentLenght" placeholder="What's happening" :maxlength="maxContentCharsCount" required autofocus></textarea>
        <span class="char-counter" v-if="contentCharsCount > maxContentCharsCount * 0.8">{{ contentCharsCount }}/{{ maxContentCharsCount }}</span>
        <span class="char-counter" v-else>&nbsp;</span>
        <input class="button-active button-message-input" value="Post" type="submit" @click="txBroadcasting=true" :disabled="txBroadcasting">
      </p>
      <p>&nbsp;</p>
    </form>
</template>


<script>
  export default {
      name: 'PostNewForm',
      mixins: [],
      components: {
      },
      data () {
        return {
          maxContentCharsCount: 217,
          contentCharsCount: 0,
          content: null
        }
      },
      methods: {
        calculateContentLenght: function() {
          this.$nextTick(() => {
            if (this.content) {
              if (this.content.length > this.maxContentCharsCount) {
                this.content = this.content.substr(0, this.maxContentCharsCount)
              }
              this.contentCharsCount = this.content.length
            }
          });
        },
        newItem (content) {
          if (this.loginRequired()) {
            this.txBroadcasting = false
            return
          }
          if (!content) {
            this.txBroadcasting = false
            return
          }
          this.newPost(content)
          this.content = ''
          this.closeModal('newModal')
        }
      }
  };
</script>

<style>
</style>



