  addressesActionsCreated (context, [ newAddresses ]) {
    if (newAddresses.length > 0) {
      console.log('addressesActionsCreated-store')
      let existingActionsCreated = context.state.actionsCreated
      let findAddressesQueryArray =  []
      for (let i = 0; i < newAddresses.length; ++i) {
        if (existingActionsCreated[newAddresses[i]] === undefined) {
          findAddressesQueryArray.push(newAddresses[i])
        }
      }
      let query = {
        'v': 3,
        'q': {
          'aggregate': [{
              '$match': {
                '$and': [{
                    'out.b0': {
                      'op': 106
                    }
                  },
                  {'out.h1': {'$in': ['6d02', '6d03', '6d04', '6d06', '6d07', '6d0b', '6d0c', '6d10', '6d13', '6d14']}},
                  {'in.e.a': {'$in': findAddressesQueryArray}}
                ]
              }
            },
            {
              '$group': {
                '_id': {'$arrayElemAt': ['$in.e.a', 0]},
                'count': {
                  '$sum': 1
                }
              }
            }
          ]
        }
      }

      let b64 = Base64.encode(JSON.stringify(query))
      let url = window.bitdbNode + b64

      let header
      if (window.bitdbApiKey) {
        header = {
          headers: { key: window.bitdbApiKey }
        }
      }

      axios
        .get(url, header)
        .then(response => {
          if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
            let result_a = []
            if (response.data.u !== undefined) {
              result_a = result_a.concat(response.data.u)
            }
            if (response.data.c !== undefined) {
              result_a = result_a.concat(response.data.c)
            }
            let result = result_a.reduce(function(acc, curr) {
              if (curr._id) {
                var fromMap = acc.map[curr._id];
                if (!fromMap) {
                  acc.map[curr._id] = fromMap = {
                    count: 0,
                    _id: curr._id
                  }
                  acc.result.push(fromMap)
                }
                fromMap.count += parseFloat(curr.count);
              } else {
                acc.result.push(curr);
              }
              return acc
            }, {
              map: {},
              result: []
            }).result

            let actionsCreated = {}
            for (let i = 0; i < result.length; ++i) {
              if (existingActionsCreated[result[i]._id] === undefined && actionsCreated[result[i]._id] === undefined) {
                actionsCreated[result[i]._id] = result[i].count
              }
            }
            context.commit('setActionsCreated', Object.assign({}, context.state.actionsCreated, actionsCreated))
            context.commit('setActionsBalances', Object.assign({}, context.state.actionsBalances))
            context.commit('setCrawledAddresses', { 'oldCrawledAddresses': context.state.crawledAddresses, 'newCrawledAddresses': newAddresses })
          }
        })
        .catch(error => {
          console.log(JSON.stringify(error))
        })
    }
  },
  addressesActionsReceived (context, [ newAddresses ]) {
    if (newAddresses.length > 0) {
      console.log('addressesActionsReceived-store')
    }
  },
  addressesActionsSent (context, [ newAddresses ]) {
    if (newAddresses.length > 0) {
      console.log('addressesActionsSent-store')
      let existingActionsSent = context.state.actionsSent
      let findAddressesQueryArray =  []
      for (let i = 0; i < newAddresses.length; ++i) {
        if (existingActionsSent[newAddresses[i]] === undefined) {
          findAddressesQueryArray.push(newAddresses[i])
        }
      }
      let query = {
        'v': 3,
        'q': {
          'aggregate': [{
              '$match': {
                '$and': [{
                    'out.b0': {
                      'op': 106
                    }
                  },
                  {'out.h1': {'$in': ['505354FF']}},
                  {'out.h2': {'$in': ['01']}},
                  {'in.e.a': {'$in': findAddressesQueryArray}}
                ]
              }
            },
            {'$unwind': '$out'},
            {
              '$group': {
                '_id': {'$arrayElemAt': ['$in.e.a', 0]},
                'count': {
        					'$sum': {
                    '$cond': [
                      { '$ne': [ '$out.e.a', {'$arrayElemAt': ['$in.e.a', 0]} ] },
                          '$out.e.v',
                          0
                    ]
        					}
                }
              }
            }
          ]
        }
      }

      let b64 = Base64.encode(JSON.stringify(query))
      let url = window.bitdbNode + b64

      let header
      if (window.bitdbApiKey) {
        header = {
          headers: { key: window.bitdbApiKey }
        }
      }

      axios
        .get(url, header)
        .then(response => {
          if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
            let result_a = []
            if (response.data.u !== undefined) {
              result_a = result_a.concat(response.data.u)
            }
            if (response.data.c !== undefined) {
              result_a = result_a.concat(response.data.c)
            }
            let result = result_a.reduce(function(acc, curr) {
              if (curr._id) {
                var fromMap = acc.map[curr._id];
                if (!fromMap) {
                  acc.map[curr._id] = fromMap = {
                    count: 0,
                    _id: curr._id
                  }
                  acc.result.push(fromMap)
                }
                fromMap.count += parseFloat(curr.count);
              } else {
                acc.result.push(curr);
              }
              return acc
            }, {
              map: {},
              result: []
            }).result

            let actionsSent = {}
            for (let i = 0; i < result.length; ++i) {
              if (existingActionsSent[result[i]._id] === undefined && actionsSent[result[i]._id] === undefined) {
                actionsSent[result[i]._id] = result[i].count
              }
            }
            context.commit('setActionsSent', Object.assign({}, context.state.actionsSent, actionsSent))
            context.commit('setActionsBalances', Object.assign({}, context.state.actionsBalances))
            context.commit('setCrawledAddresses', { 'oldCrawledAddresses': context.state.crawledAddresses, 'newCrawledAddresses': newAddresses })
          }
        })
        .catch(error => {
          console.log(JSON.stringify(error))
        })
    }
  },
