  likesCountPerPost (context, [ newTxIds ]) {
    if (newTxIds.length > 0) {
      console.log('likesCountPerPost-store')
      let queryMatch = {}
      queryMatch['$and'] = []
      queryMatch['$and'].push({'out.b0':{ 'op': 106 }})
      queryMatch['$and'].push({'out.h1':'6d04'})

      let txIdsSearchArray = []
      for (let i = 0; i < newTxIds.length; ++i) {
        txIdsSearchArray.push(reverseHex(newTxIds[i]))
      }
      queryMatch['$and'].push({'out.h2': {'$in': txIdsSearchArray}})

      let query = {
        'v': 3,
        'q': {
          'aggregate': [{
            '$match': queryMatch
          },
          {
            '$project': {
              'txId': {
                '$cond': {
                  'if': {
                    '$eq': [{
                      "$strLenCP": {
                        '$arrayElemAt': ['$out.b2', 0]
                      }
                    }, 44]
                  },
                  'then': {
                    '$arrayElemAt': ['$out.b2', 0]
                  },
                  'else': {
                    '$cond': {
                      'if': {
                        '$eq': [{
                          "$strLenCP": {
                            '$arrayElemAt': ['$out.b2', 1]
                          }
                        }, 44]
                      },
                      'then': {
                        '$arrayElemAt': ['$out.b2', 1]
                      },
                      'else': {
                        '$arrayElemAt': ['$out.b2', 2]
                      }
                    }
                  }
                }
              }
            }
          },
          {
            '$group': {
              '_id': '$txId',
              'count': {
                '$sum': 1
              }
            }
          }],
          'limit': 5000
        }
      }

      let b64 = Base64.encode(JSON.stringify(query))
      let url = window.bitdbNode + b64

      let header
      if (window.bitdbApiKey) {
        header = {
          headers: { key: window.bitdbApiKey }
        }
      }

      axios
        .get(url, header)
        .then(response => {
          if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
            let result_a = []
            if (response.data.u !== undefined) {
              result_a = result_a.concat(response.data.u)
            }
            if (response.data.c !== undefined) {
              result_a = result_a.concat(response.data.c)
            }
            let result = result_a.reduce(function(acc, curr) {
              if (curr._id) {
                var fromMap = acc.map[curr._id];
                if (!fromMap) {
                  acc.map[curr._id] = fromMap = {
                    count: 0,
                    _id: curr._id
                  }
                  acc.result.push(fromMap);
                }
                fromMap.count += parseFloat(curr.count);
              } else {
                acc.result.push(curr);
              }
              return acc;
            }, {
              map: {},
              result: []
            }).result;

            let likesCount = {}
            for (let i = 0; i < result.length; ++i) {
              const b = new Buffer(result[i]._id, 'base64')
              let txId = reverseHex(b.toString('hex'))
              likesCount[txId] = result[i].count
            }
            context.commit('setLikesCount', Object.assign({}, context.state.likesCount, likesCount))
          }
        })
        .catch(error => {
          console.log(JSON.stringify(error))
//          this.memoErrored = true
        })
    }
  },

