<template>
  <div class="grid grey">
    <HeaderNav/>

    <div class="s_left">
      <UserNav :address="requestAddress" v-if="requestAddress" />
    </div>

    <div  class="s_results"  v-infinite-scroll="loadMore" infinite-scroll-disabled="scrollLoading" infinite-scroll-distance="500">
      <p class="section-title" v-if="requestAddress">
        Posts by
        <span v-if="usernames[requestAddress]">{{ usernames[requestAddress] }}</span>
        <span v-else>{{ requestAddress.substr(0, 12) }}...</span>
      </p>
      <div v-if="loading === false">
          <PostListItem
            v-for="(item, index) in resultBitdbItems"
            :key="index"
            :item="item">
          </PostListItem>
          <div class="n_results" v-bind:class="{ 'preloader': !streamEnd }"></div>
      </div>
      <div class="s_results" v-else>
        <div class="n_results" v-bind:class="{ 'preloader': !streamEnd }"></div>
      </div>
    </div>

    <div class="s_right topics">
    </div>

    <Footer/>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import HeaderNav from '@/components/Header/Nav'
import Footer from '@/components/Footer'
import UserNav from '@/components/User/Nav'
import PostListItem from '@/components/Post/ListItem'

export default {
  name: 'UserPosts',
  mixins: [],
  props: {
    txId: String,
    address: String,
  },
  data () {
    return {
      userLegacyAddress: window.convertAddress(this.getAddress(), 'legacy'),
      requestLegacyAddress: window.convertAddress(this.address, 'legacy'),
      requestAddress: window.convertAddress(this.address),
      currentItem: null,
      item: null,
      offset: 0,
      page: 1,
      itemsPerPage: 20,
      resultBitsocketItems: [],
      resultBitdbItems: [],
      loading: false,
      errored: false,
      streamEnd: false,
      scrollLoading: false
    }
  },
  head: function () {
    return {
      title: {
        inner: 'Post by ' + (this.usernames[this.requestAddress] ? this.usernames[this.requestAddress] : this.requestLegacyAddress)
      },
      // Meta tags
      meta: [
        { name: 'application-name', content: 'Poster.cash' },
        { name: 'description', content: 'All post by ' + (this.usernames[this.requestAddress] ? this.usernames[this.requestAddress] : this.requestLegacyAddress) + ' on Poster.cash', id: 'desc' },
        { name: 'twitter:title', content: 'Post by ' + (this.usernames[this.requestAddress] ? this.usernames[this.requestAddress] : this.requestLegacyAddress) },
        { n: 'twitter:description', c: 'All post by ' + (this.usernames[this.requestAddress] ? this.usernames[this.requestAddress] : this.requestLegacyAddress) + ' on Poster.cash' },
        { itemprop: 'name', content: 'Post by ' + (this.usernames[this.requestAddress] ? this.usernames[this.requestAddress] : this.requestLegacyAddress) },
        { itemprop: 'description', content: 'All post by ' + (this.usernames[this.requestAddress] ? this.usernames[this.requestAddress] : this.requestLegacyAddress) + ' on Poster.cash' },
        { property: 'og:title', content: 'Post by ' + (this.usernames[this.requestAddress] ? this.usernames[this.requestAddress] : this.requestLegacyAddress) },
      ]
    }
  },
  components: {
    HeaderNav,
    Footer,
    UserNav,
    PostListItem
  },
  mounted () {
    this.page = parseInt(this.offset/this.itemsPerPage) + 1
    if (this.loading === false && this.requestAddress) {
      this.searchTheBlockchainForPosts()
    }
    if (this.txId) {
      this.getDisplayItem()
    }
  },
  created() {
  },
  computed: {
    ...mapState([
      'localPost',
      'followedUsers',
      'mutedUsers',
      'myPostLikes',
      'myPostReplies',
      'profilePictures',
      'usernames',
      'likesCount',
      'repliesCount'
    ])
  },
  watch: {
    localPost: function (val) {
      if (this.localPost !== null && userLegacyAddress && window.convertAddress(this.localPost.sender, 'legacy') === userLegacyAddress) {
        this.resultBitdbItems.unshift(this.localPost)
        this.changeLocalPost(null)
      }
    },
    requestAddress: function (val) {
      if (this.loading === false) {
        this.searchTheBlockchainForPosts()
      }
    }
  },
  methods: {
    ...mapActions([
      'addNewUsersData',
      'changeLocalPost',
      'changeCurrentPost'
    ]),
    getDisplayItem () {
      this.errored = false
      let query = {
        'v': 3,
        'q': {
          'find': {
            '$and': [{
                'out.b0': {
                  'op': 106
                }
              }, {
                'tx.h': this.txId
              }
            ]
          },
          'project': { 'in': 1, 'out.$': 1, 'blk': 1, 'tx': 1 },
          'skip': 0,
          'limit': 1
        },
        'r': {
          'f': '[ .[] | {prefix: .out[0].h1, sender: .in[0].e.a, parentTxId: .out[0].h2, content1: .out[0].s2, content2: .out[0].s3, timestamp: .blk.t?, txId: .tx.h} ]'
        }
      }

      let b64 = Base64.encode(JSON.stringify(query))
      let url = window.bitdbNode + b64

      let header
      if (window.bitdbApiKey) {
        header = {
          headers: { key: window.bitdbApiKey }
        }
      }

      axios
        .get(url, header)
        .then(response => {
          if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
            let results = []
            if (response.data.u !== undefined) {
              results = results.concat(response.data.u.reverse())
            }
            if (response.data.c !== undefined) {
              results = results.concat(response.data.c)
            }

            this.item = results[0]
            this.requestAddress = this.item.sender
            let host = window.location.href.replace(window.location.pathname,'');
            sessionStorage.setItem('oldUrl', host + '/user/' + window.convertAddress(this.item.sender, 'legacy'))
            if (this.usernames[this.item.sender]) {
              sessionStorage.setItem('oldTitle', 'Posts by ' + this.usernames[this.item.sender])
            } else {
              sessionStorage.setItem('oldTitle', 'Posts by ' + window.convertAddress(this.item.sender, 'legacy'))
            }
            this.changeCurrentPost(this.item)
            this.openModal('displayModal')
          }
        })
        .catch(error => {
          this.errored = true
        })
    },
    searchTheBlockchainForPosts (offset = 0) {
      if (offset === 0) {
        this.loading = true
      }
      this.errored = false

      let query = {
        'v': 3,
        'q': {
          'find': {
            '$and': [{
                'out.b0': {
                  'op': 106
                }
              },
              {'out.h1': {'$in': ['6d02', '6d03', '6d0c']}},
              {
                'in.e.a': this.requestAddress
              }
            ]
          },
          'project': { 'in': 1, 'out.$': 1, 'blk': 1, 'tx': 1 },
          'skip': parseInt(offset),
          'limit': this.itemsPerPage
        },
        'r': {
          'f': '[ .[] | {prefix: .out[0].h1, sender: .in[0].e.a, parentTxId: .out[0].h2, content1: .out[0].s2, content2: .out[0].s3, timestamp: .blk.t?, txId: .tx.h} ]'
        }
      }

      let b64 = Base64.encode(JSON.stringify(query))
      let url = window.bitdbNode + b64

      let header
      if (window.bitdbApiKey) {
        header = {
          headers: { key: window.bitdbApiKey }
        }
      }

      axios
        .get(url, header)
        .then(response => {
          if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
            let newBitdbItems = []
            if (response.data.u !== undefined) {
              newBitdbItems = newBitdbItems.concat(response.data.u.reverse())
            }
            if (response.data.c !== undefined) {
              newBitdbItems = newBitdbItems.concat(response.data.c)
            }

            // populate usernames and txIds
            let newAddresses = []
            let newTxIds = []
            for (let i = 0; i < newBitdbItems.length; ++i) {
              if(newAddresses.indexOf(newBitdbItems[i].sender) === -1) {
                newAddresses.push(newBitdbItems[i].sender)
              }
              if(newTxIds.indexOf(newBitdbItems[i].txId) === -1) {
                newTxIds.push(newBitdbItems[i].txId)
              }
            }

            this.addNewUsersData({ 'address': this.getAddress(), 'newTxIds': newTxIds, 'newAddresses': [...newAddresses.concat([ this.requestAddress ])] })
            this.resultBitdbItems = this.resultBitdbItems.concat(newBitdbItems)

            if (response.data.c.length < this.itemsPerPage) {
              this.streamEnd = true
            }
          }
          this.offset += this.itemsPerPage
          this.loading = false
          this.scrollLoading = false;
        })
        .catch(error => {
          this.errored = true
          this.loading = false
        })
    },
    loadMore() {
      this.scrollLoading = true;
      if (this.streamEnd === false && this.errored === false && this.loading === false && this.requestAddress) {
        this.searchTheBlockchainForPosts(this.offset)
      }
    }
  }
}
</script>

<style>
</style>



