<template>
  <div class="grid grey">
    <HeaderNav/>

    <div class="s_left">
      <UserNav :address="userAddress" />
    </div>

    <div  class="s_results">
      <p class="section-title">
        Messages
      </p>
      <span v-if="loading === false">
        <span  v-if="resultBitdbItems.length > 0">

        </span>
        <span v-else>
          <div class="n_results">No messages</div>
        </span>

        <MessageForm :publicKey="publicKey" v-if="publicKey" />
      </span>
      <span v-else>
      <div class="n_results" v-bind:class="{ 'preloader': !streamEnd }"></div>
      </span>
    </div>

    <div class="s_right topics">
    </div>

    <Footer/>
  </div>
</template>

<script>
import eccrypto from 'eccrypto'
import { publicKeyConvert} from 'secp256k1'

import HeaderNav from '@/components/Header/Nav'
import Footer from '@/components/Footer'
import UserNav from '@/components/User/Nav'

import MessageForm from '@/components/Messages/NewForm'

export default {
  name: 'UserMessagesThread',
  mixins: [ ],
  props: {
    address: String,
  },
  data () {
    return {
      userAddress: window.convertAddress(this.getAddress()),
      requestAddress: window.convertAddress(this.address),
      offset: 0,
      page: 1,
      itemsPerPage: 20,
      resultBitdbItems: [],
      publicKey: null,
      loading: false,
      errored: false,
      streamEnd: false,
      scrollLoading: false
    }
  },
  head: function () {
    return {
      title: {
        inner: 'Messages'
      },
      // Meta tags
      meta: [
        { name: 'application-name', content: 'Poster.cash' },
      ]
    }
  },
  components: {
    HeaderNav,
    Footer,
    UserNav,
    MessageForm
  },
  mounted () {
    this.loginRequired()
    this.getPublicKeyForAddress(this.requestAddress)

// https://github.com/ethers-io/ethers.js/issues/232
    var privateKeyB = this.calculatePrivateKeyBuffer()
    var publicKeyB = eccrypto.getPublic(privateKeyB)
    var compressedPublicKey = '025e108348d065924625988ae7e6606a1c051dd650b90bb31c601f9e91569eb22c'
    var compressedPublicKeyB = Buffer.from(compressedPublicKey, 'hex')

    // Encrypting the message for B.
    eccrypto.encrypt(compressedPublicKeyB, Buffer("poster.cash")).then(function(encryptedBuffers) {
      const cipher = {
          iv: encryptedBuffers.iv.toString('hex'),
          ephemPublicKey: encryptedBuffers.ephemPublicKey.toString('hex'),
          ciphertext: encryptedBuffers.ciphertext.toString('hex'),
          mac: encryptedBuffers.mac.toString('hex')
      };
      const compressedKey = publicKeyConvert(new Buffer(cipher.ephemPublicKey, 'hex'), true).toString('hex')
      const ret = Buffer.concat([
          new Buffer(cipher.iv, 'hex'), // 16bit
          new Buffer(compressedKey, 'hex'), // 33bit
          new Buffer(cipher.mac, 'hex'), // 32bit
          new Buffer(cipher.ciphertext, 'hex') // var bit
      ]).toString('hex')
      console.log(ret.length)
      console.log(ret)
    //end encrypt to text

    // text to encrypt
    const buf = new Buffer(ret, 'hex');
    var encryptedObj = {
        iv: buf.toString('hex', 0, 16),
        ephemPublicKey: buf.toString('hex', 16, 49),
        mac: buf.toString('hex', 49, 81),
        ciphertext: buf.toString('hex', 81, buf.length)
    };
    encryptedObj.ephemPublicKey = publicKeyConvert(new Buffer(encryptedObj.ephemPublicKey, 'hex'), false).toString('hex')

    const encryptedBuffer = {
        iv: new Buffer(encryptedObj.iv, 'hex'),
        ephemPublicKey: new Buffer(encryptedObj.ephemPublicKey, 'hex'),
        ciphertext: new Buffer(encryptedObj.ciphertext, 'hex'),
        mac: new Buffer(encryptedObj.mac, 'hex')
    };

      // B decrypting the message.
      eccrypto.decrypt(privateKeyB, encryptedBuffer).then(function(plaintext) {
        console.log("Message to part B:", plaintext.toString())
      })
    })
  },
  created() {
  },
  computed: {
  },
  watch: {
  },
  methods: {
    getPublicKeyForAddress (address) {
      this.errored = false

      let findQuery = {
        "in.e.a": address
      }

      let query = {
        'v': 3,
        'q': {
          'find': findQuery,
          'project': { 'in': 1 },
          'skip': 0,
          'limit': 1
        },
        'r': {
          'f': '[ .[] | {publicKey: .in[0].h1} ]'
        }
      }

      let b64 = Base64.encode(JSON.stringify(query))
      let url = window.bitdbNode + b64

      let header
      if (window.bitdbApiKey) {
        header = {
          headers: { key: window.bitdbApiKey }
        }
      }

      axios
        .get(url, header)
        .then(response => {
          if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
            if (response.data.u !== undefined && response.data.u.length > 0) {
              this.publicKey = response.data.u[0].publicKey
              return
            }
            if (response.data.c !== undefined && response.data.c.length > 0) {
              this.publicKey = response.data.c[0].publicKey
              return
            }
            return
          }
        })
        .catch(error => {
    //          console.log(JSON.stringify(error))
          this.errored = true
        })
    },

    getMessages (offset = 0) {
      if (offset === 0) {
        this.loading = true
      }
      this.errored = false

      let query = {
        'v': 3,
        'q': {
          'find': {
            '$and': [{
                'out.b0': {
                  'op': 106
                }
              },
              {'out.s1': '1FjFiAJdhZ74NXsh9J472f7qkYjpiW9to9'},
              {'out.h2': '01'},
              { '$or': [
                {'$and': [{'out.s3': this.userAddress}, {'in.e.a': this.requestAddress}]},
                {'$and': [{'out.s3': this.requestAddress}, {'in.e.a': this.userAddress}]}
              ]}
            ]
          },
          'project': { 'in': 1, 'out.$': 1, 'blk': 1, 'tx': 1 },
          'skip': parseInt(offset),
          'limit': this.itemsPerPage
        },
        'r': {
          'f': '[ .[] | {sender: .in[0].e.a, receiver: .out[0].s3, content: .out[0].s4, timestamp: .blk.t?, txId: .tx.h} ]'
        }
      }

      let b64 = Base64.encode(JSON.stringify(query))
      let url = window.bitdbNode + b64

      let header
      if (window.bitdbApiKey) {
        header = {
          headers: { key: window.bitdbApiKey }
        }
      }

      axios
        .get(url, header)
        .then(response => {
          if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
            let newBitdbItems = []
            if (response.data.u !== undefined) {
              newBitdbItems = newBitdbItems.concat(response.data.u.reverse())
            }
            if (response.data.c !== undefined) {
              newBitdbItems = newBitdbItems.concat(response.data.c)
            }

            // populate usernames
            let newAddresses = []
            for (let i = 0; i < newBitdbItems.length; ++i) {
              if(newAddresses.indexOf(newBitdbItems[i].sender) === -1) {
                newAddresses.push(newBitdbItems[i].sender)
              }
              if(newAddresses.indexOf(newBitdbItems[i].receiver) === -1) {
                newAddresses.push(newBitdbItems[i].receiver)
              }
            }

            this.addNewUsersData({ 'address': this.getAddress(), 'newTxIds': [], 'newAddresses': [...newAddresses.concat([ this.requestAddress ])] })
            newBitdbItems = newBitdbItems.reverse()
            this.resultBitdbItems = newBitdbItems.concat(this.resultBitdbItems)

            if (response.data.c.length < this.itemsPerPage) {
              this.streamEnd = true
            }
          }
          this.offset += this.itemsPerPage
          this.loading = false
          this.scrollLoading = false;
        })
        .catch(error => {
          this.errored = true
          this.loading = false
        })
    },
    loadMore() {
      this.scrollLoading = true;
      if (this.streamEnd === false && this.errored === false && this.loading === false && this.requestAddress) {
        this.getMessages(this.offset)
      }
    }
  }
}
</script>

<style>
</style>



